import { Component, OnDestroy } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../services/http.service';

// Interface
import { Account } from '../interface/account';
import { Ticket } from '../../tickets/interface/ticket';

@Component({
  moduleId: module.id,
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class AccountsListComponent implements OnDestroy {

  tickets: Ticket[] = [];
  accounts: Account[] = [];

  dataForRequestSearchComp = {
    "table": "accounts",
    "parameters": ["firstname", "lastname", "email", "nickname"],
    "condition": "deleted=is.false"
  };

  sub = new Subscription();
  config: any;

  constructor(
    private service: Service
  ) {
    this.loadAllItems("tickets", "tickets", "all");
    this.loadAllItems("accounts", "accounts", "not_deleted");
    this.config = {
			currentPage: 1,
			itemsPerPage: 10
    };
  }

  loadAllItems(table, variable, condition) {
    let subscription = this.service.getAll(table, condition).subscribe(
      data => {
        this[variable] = data;
      }
    );

    this.sub.add(subscription);
  }

  overWriteAccounts(data) {
    this.accounts = data;
    this.config.currentPage = 1;
    if(data.length >= 10) {
      this.config.itemsPerPage = 10
    } else {
      this.config.itemsPerPage = data.length;
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



