import { Component, OnDestroy, Input, Output, EventEmitter, ViewChild, ElementRef } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Helpers
import { md5 } from '../../../../helpers/md5';

// Interfaces
import { Account } from '../../interface/account';

// Services
import { Service } from '../../../../services/http.service';
import { MessageService } from '../../../../services/message.service';
import { AccountService } from '../../service/account.service';

@Component({
  selector: 'account-password-change',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.scss']
})
export class AccountPasswordChangeComponent implements OnDestroy {

  @Input() account: Account;
  @ViewChild('new_psw') new_psw:ElementRef;
  @ViewChild('confirm_new_psw') confirm_new_psw:ElementRef;
  @ViewChild('message') message:ElementRef;
  @ViewChild('sub_button') sub_button:ElementRef;

  sub = new Subscription();

  constructor(
    private service: Service,
    private accountService: AccountService,
    private messageService: MessageService
  ) { }

  // Check if password inserted by user is correct
  checkIfRecordExist(form) {
    let query = "id=eq." + this.account.id + "&" + "psw=eq." + md5(form.value.current_psw);
    let subscription = this.service.getRecord("accounts", query).subscribe(
      data => {
        this.update({
          "psw": md5(form.value.new_psw),
          "confirm_psw": md5(form.value.confirm_new_psw)
        });
      },
      err => {
        this.messageService.setMessage("psw-rejected");
      }
    );

    this.sub.add(subscription);
  }

  update(data) {
    let query = "id=eq." + this.account.id;
    if(data.psw == data.confirm_psw) {
      let subscription = this.service.update("accounts", query, data).subscribe(
        data => {
          this.accountService.logout(this.account);
        },
        err => {
          this.messageService.setMessage("rejected");
        }
      );
      this.sub.add(subscription);
    } else {
      this.messageService.setMessage("confirm_incorrect_psw");
    }
  }

  checkPassword() {
    let message = this.message.nativeElement;

    let password_value = this.new_psw.nativeElement.value;
    let el_password = this.new_psw.nativeElement;

    let confirm_password_value = this.confirm_new_psw.nativeElement.value;
    let el_confirmPassword = this.confirm_new_psw.nativeElement;

    if(password_value && confirm_password_value) {
      if(confirm_password_value !== password_value) {
        el_confirmPassword.style.borderColor = 'red';
        message.textContent = 'Conferma password errata';
        message.style.color = 'red';
        this.sub_button.nativeElement.setAttribute('disabled', true);
      } else {
        el_confirmPassword.style.borderColor = 'green';
        message.style.color = 'green';
        message.textContent = 'Password confermata';
        this.sub_button.nativeElement.removeAttribute('disabled');
      }
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



