import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Subscription } from "rxjs/Subscription";
declare var $:any;

// Services
import { Service } from '../../../services/http.service';
import { AccountService } from '../../accounts/service/account.service';
import { EventSourceListenerService } from '../../../services/event-source-listener.service';
import { NotificationService } from '../../widgets/dropdown-icon-nav/service/notification.service';

// Interfaces
import { Note } from '../../notes/interface/note';

// Helpers
import { Timezone } from '../../../helpers/timezone';
import { LocalStorageService } from '../../../helpers/local-storage';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit, OnDestroy {

  @Input() account: any;
  notes: any = [];

  notifications: boolean = false;

  sub = new Subscription();
  current_day: string = "";

  constructor(
    private service: Service,
    private accountService: AccountService,
    private timezoneHelper: Timezone,
    private eventSourceListenerService: EventSourceListenerService,
    private lsService:LocalStorageService,
    private notificationService: NotificationService
  ) {
    this.current_day = this.timezoneHelper.splitTimeZoneDate(this.timezoneHelper.getCurrentDate());

    // notes
    this.getCurrentDayNotes();

  }

  showNotificationsSidebar() {
    $(".notify-alert").toggleClass("hide");
  }

  getCurrentDayNotes() {
    let account = JSON.parse(this.lsService.get("account"));
    let query = "account_id=eq." + account.id + "&archived=" + "is.false" + "&alarm_date=eq." + this.current_day;
    let subscription = this.service.getRecords("notes", query).subscribe(
      data => {
        this.notificationService.set(data);
        //this.notes = data;
      },
      err => {
        console.log(err);
      }
    );

    this.sub.add(subscription);
  }

  hideSidebar() {
    $("#aside").toggleClass("hide");
    $("#main-container").toggleClass("hide");
    $("#header").toggleClass("hide");
  }

  ngOnInit() {
    // Tickets notifications
    let subscription = this.eventSourceListenerService.item$.subscribe(
      data => {
        if(data.length) {
          if(this.account.settings[0].tickets_notifications === true) {
            if(this.areThereTicketNotVisitedYet(data)) {
              this.notifications = true;
            } else {
              this.notifications = false;
            }
          }
        } else {
          this.notifications = false;
        }
      }
    );

    this.sub.add(subscription);
  }

  areThereTicketNotVisitedYet(list) {
    let sublist = list.map(function(item) {
      return item.read
    });
    if(sublist.includes(false)) {
      return true
    } else {
      return false
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



