import { Component, OnInit, OnDestroy, AfterViewInit, ViewChild } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Components
import { FilterFormComponent } from '../filter-form/filter-form.component'

// Services
import { Service } from '../../../services/http.service';
import { AccountService } from '../../accounts/service/account.service';
import { TicketsService } from '../service/tickets.service';
import { EventSourceListenerService } from '../../../services/event-source-listener.service';

// Interface
import { Ticket } from '../interface/ticket';
import { Account } from '../../accounts/interface/account';

// Helpers
import { SessionStorageService } from '../../../helpers/session-storage';
import { LocalStorageService } from '../../../helpers/local-storage';
import { Timezone } from '../../../helpers/timezone';
import { CheckObject } from '../../../helpers/check-object';
import { RequestUrl } from '../../../helpers/build-request-url';

@Component({
  selector: 'tickets-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class TicketListComponent implements OnInit, OnDestroy, AfterViewInit {

  account: Account;
  accounts: Account[] = [];
  tickets: any[];

  sub = new Subscription();
  p: number = 1;

  @ViewChild(FilterFormComponent) filter_form: FilterFormComponent;

  constructor(
    private service: Service,
    private lsService: LocalStorageService,
    private ssService: SessionStorageService,
    private accountService: AccountService,
    private timezone: Timezone,
    public buildUrl: RequestUrl,
    public checkObject: CheckObject,
    private ticketsService: TicketsService,
    private eventSourceListenerService: EventSourceListenerService
  ) {
    this.account = JSON.parse(this.lsService.get("account"));
    this.getAccounts();
  }

  getAccounts() {
    let query = "deleted=" + "is.false" + "&status=" + "eq.1" + "&order=" + "id.desc" + "&select=id, firstname, lastname, avatar_color";
    let subscription = this.service.getRecords("accounts", query).subscribe(
      data => {
        this.accounts = data;
      }
    );

    this.sub.add(subscription);
  }

  savePageNumber(number) {
    console.log(number);
    this.p = number;
    this.ssService.set([
      {
        'key': 'filter_page_number',
        'value': JSON.stringify(this.p)
      }
    ]);
  }

  urlComposition(data) {
    let query: string = "archived=" + "is.false";
    if(!this.checkObject.isBlank(data)) {
      if(data["assignees_id"] !== "all" && data["assignees_id"] !== undefined) {
        let copy = Object.assign({}, data);
        if(data["created_by"] == "all") {
          delete copy["created_by"];
          query += "&" + this.buildUrl.build(copy);
        } else {
          query += "&" + this.buildUrl.build(data);
        }
        if(this.account.profile != 0) {
          if(data["assignees_id"] != this.account.id) {
            query += "&" + "created_by=eq." + this.account.id;
          } else {
            if(data["status"] == undefined) {
              query += "&and=(" + "status.neq." + 3 + "," + "status.neq." + 4 + ")";
            }
          }
        }
      } else if(data["created_by"] !== "all" && data["created_by"] !== undefined) {
        let copy = Object.assign({}, data);
        if(data["assignees_id"] == "all") {
          delete copy["assignees_id"];
          query += "&" + this.buildUrl.build(copy);
        } else {
          query += "&" + this.buildUrl.build(data);
        }
      } else {
        if(data["status"]) {
          let obj = {
            "status": data["status"]
          }
          query += "&" + this.buildUrl.build(obj);
        };
        if(this.account.profile != 0) {
          query += "&" + "created_by=eq." + this.account.id;
        }
      }
    }
    query += "&order=" + "commented_at.desc" + "&select=*, reporter: accounts(id, firstname, lastname)";
    return query;
  }

  getOwnTickets() {
    let _self = this;
    let query: string = "";
    let filtering_data = JSON.parse(this.ssService.get("filter"));
    if(filtering_data) {
      query = this.urlComposition(filtering_data);
      this.filter_form.filter_settings = filtering_data;
    } else {
      query = "or=(" + "and(" + "created_by.eq." + this.account.id + "," + "assignees_id.not.cs." + "{" + this.account.id + "})" + "," + "and(" + "assignees_id.cs." + "{" + this.account.id + "}" + "," + "status.neq." + 3 + "," + "status.neq." + 4 + ")" + ")" + "&archived=" + "is.false" + "&order=" + "commented_at.desc" + "&select=*, reporter: accounts(id, firstname, lastname)";
    }
    let subscription = this.service.getRecords("tickets", query).subscribe(
      data => {
        // console.log(data);
        this.tickets = data;
      },
      err => {
        console.log(err);
      },
      () => {
        this.tickets.map(function(ticket) {
          ticket.accounts = ticket.assignees_id.map(function(assignee_id) {
            return _self.accounts.find(function(account) {return assignee_id === account.id});
          }).filter(function(elem) {
            if(elem === undefined) {
              console.log("è undefined");
              ticket.avatar_color = "red";
              ticket.firstname = "Bracco";
              ticket.lastname = "Bracco";
            }
            return elem !== undefined;
          });
          if(ticket.expire_at) {
            ticket.deadline_at = _self.timezone.daysLeftToDeadline(new Date(), ticket.expire_at);
          }
          return ticket;
        });
      }
    );

    this.sub.add(subscription);
  }

  ngOnInit() {
    this.getOwnTickets();
    let page_number = this.ssService.get("filter_page_number");
    console.log(page_number);
    if(page_number != "") {
      this.p = +page_number;
    }
  }

  ngAfterViewInit() {
    let sseListener = this.eventSourceListenerService.item$.subscribe(
      data => {
        //console.log(data);
        let _self = this;
        //this.notification_table = "comments";
        if(data.length > 0) {
          data.map(function(elem) {
            //console.log(elem);
            if(elem.table == "comments" || elem.table == "tickets") {
              //console.log("entrato nell if");
              _self.getOwnTickets();
              return true
            }
          });
        }
      }
    );
    this.sub.add(sseListener);

    let subscription = this.ticketsService.activated_ticket.subscribe(
      res => {
        if(Object.keys(res).length > 0){
          this.tickets.unshift(res);
        }
      }
    );
    this.sub.add(subscription);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



