import { Component, Input } from '@angular/core';

// Services
import { EventSourceListenerService } from '../../../../services/event-source-listener.service';

// Helpers
import { NavigationService } from '../../../../helpers/navigation';

@Component({
  selector: 'notify-alert-item',
  templateUrl: './item.component.html',
  styleUrls: ['./item.component.scss']
})
export class NotifyAlertItemComponent {

  @Input() message;

  constructor(
    private eventSourceListenerService: EventSourceListenerService,
    private navigation: NavigationService
  ) {}

  setStatus() {
    this.message.read = true;
    this.eventSourceListenerService.updateElement(this.message);
    if(this.message.operation != 'DELETE') {
      this.navigation.redirect("/" + this.message.table + "/" + this.message.ticket_id);
    } else {
      this.navigation.redirect("/");
    }
  }

  removeIt() {
    this.eventSourceListenerService.removeElementFromObservableArray(this.message);
  }

}



