import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../services/http.service';
import { AccountService } from '../../accounts/service/account.service';

// Helpers
import { LocalStorageService } from '../../../helpers/local-storage';

// Interface
import { Document } from '../interface/document';
import { Account } from '../../accounts/interface/account';

@Component({
  selector: 'documents-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class DocumentListComponent implements OnInit, OnDestroy {

  account_id: number;
  account_profile: number;
  accounts: Account[] = [];
  documents: Document[] = [];

  sub = new Subscription();
  config: any;

  constructor(
    private lsService: LocalStorageService,
    private service: Service,
    private accountService: AccountService
  ) {
    this.account_id = +this.lsService.get("accountId");
    this.account_profile = +this.lsService.get("accountProfile");
    this.loadAllItems("accounts", "accounts", "active_and_not_deleted");

    this.config = {
      id: 'documents-list',
			currentPage: 1,
			itemsPerPage: 10
    };
  }

  get_documents() {
    let query = "share_with=cs." + "{" + this.account_id + "}" + "&archived=" + "is.false" + "&order=" + "id.desc" + "&select=*, by_account: accounts(id, firstname, lastname)";
    let subscription = this.service.getRecords("documents", query).subscribe(
      data => {
        let accounts = this.accounts;
        this.documents = data;
        this.documents.map(function(document) {
          document.accounts = [];
          document.share_with.forEach(function(account) {
            let obj = accounts.find(function(elem) {
              return elem.id == account
            });
            let copy = {
              "id": obj.id,
              "firstname": obj.firstname,
              "lastname": obj.lastname,
              "avatar_color": obj.avatar_color
            };
            document.accounts.push(copy);
          })
        });
      },
      err => {
        console.log(err);
      }
    );

    this.sub.add(subscription);
  }

  loadAllItems(table, variable, condition) {
    let subscription = this.service.getAll(table, condition).subscribe(
      data => {
        this[variable] = data;
      }
    );

    this.sub.add(subscription);
  }

  ngOnInit() {
    this.get_documents();
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



