import { Component, Input } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../../services/http.service';
import { NotificationService } from '../service/notification.service';

// Helpers
import { NavigationService } from '../../../../helpers/navigation';

@Component({
  selector: 'row-dropdown-icon-nav',
  templateUrl: './row.component.html',
  styleUrls: ['./row.component.scss']
})
export class RowDropdownIconNavComponent {

  @Input() list: any[] = [];
  @Input() item: any;
  @Input() icon_class: string;
  @Input() table: string;
  @Input() url: string;

  sub = new Subscription();

  constructor(
    private navigation: NavigationService,
    private service: Service,
    private notificationService: NotificationService
  ) {}

  setStatus() {
    let read = {
      'read': true
    };
    let query = "id=eq." + this.item.id;
    let subscription = this.service.update(this.table, query, read).subscribe(
      data => {
        this.item.read = true;
        this.notificationService.updateElement(this.item);
        this.navigation.redirect(this.url + this.item.id);
      },
      err => {
        console.log(err)
      }
    );
    this.sub.add(subscription);
  }

}



