import { Component, OnInit, OnDestroy, ViewChild, HostListener } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { FormCanDeactivate } from '../../../helpers/form-can-deactivate/form-can-deactivate';
import { NgForm } from "@angular/forms";
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../services/http.service';
import { MessageService } from '../../../services/message.service';

@Component({
  templateUrl: './details.component.html',
  styleUrls: ['./details.component.scss']
})
export class AccountDetailsComponent extends FormCanDeactivate {

  id: number;
  account: any = {};
  account_revoke: any = {};

  @ViewChild('form')
  form: NgForm;

  sub = new Subscription();

  constructor(
    private service: Service,
    public route: ActivatedRoute,
    private messageService: MessageService
  ) {
    super();
  }

  update(form) {
    let query = "id=eq." + this.account.id;
    let subscription = this.service.update("accounts", query, form.value).subscribe(
      data => {
        this.messageService.setMessage("success");
        this.account_revoke = this.account;
      },
      err => {
        this.messageService.setMessage("rejected");
      }
    );

    this.sub.add(subscription);
  }

  getAccount() {
    let subscription = this.service.getBy("accounts", "id", this.id).subscribe(
      data => {
        this.account = data;
        this.account_revoke = data;
      }
    );

    this.sub.add(subscription);
  }

  revoke(){
    // this.getAccount();
    this.account = this.account_revoke;
  }

  ngOnInit() {
    let subscription = this.route.params.subscribe(params => {
      this.id = +params['id']; // (+) converts string 'id' to a number
      this.getAccount();
    });

    this.sub.add(subscription);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



