import { Component, OnDestroy } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../services/http.service';
import { MessageService } from '../../../services/message.service';
import { AccountService } from '../../accounts/service/account.service';

// Interfaces
import { Account } from '../../accounts/interface/account';

// Helpers
import { LocalStorageService } from '../../../helpers/local-storage';
import { StringHelperService } from '../../../helpers/string-helper';
import { NavigationService } from '../../../helpers/navigation';
import { Timezone } from '../../../helpers/timezone';

@Component({
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class TicketAddComponent implements OnDestroy {

  accounts: any[] = [];
  account: Account;
  sub = new Subscription();
  loading: boolean = false;

  public editor_options: Object = {
    placeholderText: 'Inserisci testo',
    charCounterCount: false,
    toolbarVisibleWithoutSelection: false,
    toolbarSticky: false,
    toolbarButtons: ['bold', 'italic', 'underline', 'strikeThrough', '|', 'fontFamily', 'fontSize', 'color', 'paragraphStyle', '|', 'paragraphFormat', 'align', 'formatOL', 'formatUL', 'outdent', 'indent', 'quote', 'insertLink', 'insertTable', '|', 'insertHR', 'selectAll', 'clearFormatting', '|', 'undo', 'redo'],
    height: 300,
    theme: "gray",
    tooltips: true
  }

  constructor(
    private service: Service,
    private messageService: MessageService,
    private accountService: AccountService,
    private lsService: LocalStorageService,
    private stringHelper: StringHelperService,
    private navigation: NavigationService,
    private timezone: Timezone,
  ) {
    this.loadAllItems("accounts", "accounts", "active_and_not_deleted");
    this.account = JSON.parse(this.lsService.get("account"));
  }

  clears(form) {
    form.reset();
  }

  create(form) {
    form.value.expire_at = this.stringHelper.nullify(form.value.expire_at);
    form.value.assignees_id = this.stringHelper.trasformToNumber(form.value.assignees_id);
    form.value.assignees_id = "{" + form.value.assignees_id.join(",") + "}";
    form.value.created_by = this.account.id;
    form.value.updated_by = this.account.id;
    form.value.updated_at = this.timezone.getCurrentDate();
    let subscription = this.service.create("tickets", form.value).subscribe(
      data => {
        this.loading = false;
        this.messageService.setMessage('success');
        // form.reset();
        this.navigation.redirect("/tickets");
      },
      err => {
        console.log(err);
        this.loading = false;
        this.messageService.setMessage('rejected');
      }
    );

    this.sub.add(subscription);
  }

  loadAllItems(table, variable, condition) {
    let subscription = this.service.getAll(table, condition).subscribe(
      data => {
        this[variable] = data;
      }
    );

    this.sub.add(subscription);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



