import { Component, OnDestroy, Input, AfterViewInit, Output, EventEmitter, ViewChild, ElementRef } from '@angular/core';
import { Subscription } from "rxjs/Subscription";
declare var $:any;

// Services
import { Service } from '../../../../services/http.service';
import { CommentService } from '../service/comment.service';

// Helpers
import { Timezone } from '../../../../helpers/timezone';

// Interface
import { Comment } from '../interface/comment';

@Component({
  selector: 'ticket-comments-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class TicketCommentAddComponent implements AfterViewInit, OnDestroy {

  @Input() account;
  @Input() reporter;
  @Input() ticket_id: number;
  @Input() comments: Comment[];

  @ViewChild("buttons", {read: ElementRef}) buttons: ElementRef;
  @ViewChild("body_text", {read: ElementRef}) body_text: ElementRef;

  sub = new Subscription();
  loading: boolean = false;

  constructor(
    private service: Service,
    private commentService: CommentService,
    private timezone: Timezone
  ) {}

  getLocationHeaders(data) {
    let patt = /(\d+)/g;
    let comment_id = data.headers.get("location");
    let comment_id_match = comment_id.match(patt);
    return +comment_id_match[0];
  }

  add(form) {
    this.loading = true;
    let form_copy: any = new Object(form.value);
    form_copy.account_id = this.account.id;
    form_copy.ticket_id = this.ticket_id;
    let subscription = this.service.create("comments", form_copy).subscribe(
      data => {
        form_copy.id = this.getLocationHeaders(data);
        form_copy.created_at = null;
        form_copy.updated_at = null;
        form_copy.account = this.account;
        this.comments.unshift(form_copy);
        this.loading = false;
        form.reset();
        this.toggleButton("hide");
        this.body_text.nativeElement.rows = 2;
      },
      err => {
        console.log(err);
        this.loading = false;
      },
      () => {
        this.updateTicket();
        //this.commentService.changeComment(this.comments);
      }
    );

    this.sub.add(subscription);
  }

  revokeIt() {
    this.body_text.nativeElement.value = "";
    this.body_text.nativeElement.rows = 2;
    this.toggleButton("hide");
  }

  updateTicket() {
    let obj = {
      "read_by": '{}',
      "commented_at": this.timezone.getCurrentDate()
    };
    //console.log("entrato in aggiorna ticket");
    let query = "id=eq." + this.ticket_id;
    let subscription = this.service.update("tickets", query, obj).subscribe();
    this.sub.add(subscription);
  }

  toggleButton(value) {
    let buttons = this.buttons.nativeElement;
    if(value == "hide") {
      buttons.style.display = 'none';
    } else {
      buttons.style.display = 'block';
    }
  }

  ngAfterViewInit() {
    let _self = this;
    this.toggleButton("hide");
    $(this.body_text.nativeElement).on('click', function (e, editor) {
      e.currentTarget.rows = 10;
      _self.toggleButton("show");
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



