import { Component, OnDestroy, ViewChild, ElementRef } from '@angular/core';
import { md5 } from '../../../helpers/md5';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../services/http.service';
import { MessageService } from '../../../services/message.service';

// Helpers
import { NavigationService } from '../../../helpers/navigation';

@Component({
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})

export class AddAccountComponent implements OnDestroy {

  loading: boolean = false;
  account_id: number;
  profile_settings_id: number;

  sub = new Subscription();

  @ViewChild('password') password:ElementRef;
  @ViewChild('confirm_password') confirm_password:ElementRef;
  @ViewChild('message') message:ElementRef;
  @ViewChild('set_profile') set_profile:ElementRef;

  constructor(
    private service: Service,
    private messageService: MessageService,
    private navigation: NavigationService
  ) { }

  checkAccountAlreadyExist(form) {
    this.loading = true;
    let query = "nickname=eq." + form.value.nickname + "&deleted=neq." + true;
    let subscription = this.service.getRecord("accounts", query).subscribe(
      already_registered => {
        this.loading = false;
        this.messageService.setMessage("prohibition");
      },
      not_registered_yet => {
        this.loading = false;
        this.create(form);
      }
    );

    this.sub.add(subscription);
  }

  checkPassword() {
    let message = this.message.nativeElement;

    let password_value = this.password.nativeElement.value;
    let el_password = this.password.nativeElement;

    let confirm_password_value = this.confirm_password.nativeElement.value;
    let el_confirmPassword = this.confirm_password.nativeElement;

    if(password_value && confirm_password_value) {
      if(confirm_password_value !== password_value) {
        el_confirmPassword.style.borderColor = 'red';
        message.textContent = 'Conferma password errata';
        message.style.color = 'red';
      } else {
        el_confirmPassword.style.borderColor = 'green';
        message.style.color = 'green';
        message.textContent = 'Password confermata';
      }
    }
  }

  getLocationHeaders(data) {
    let patt = /(\d+)/g;
    let comment_id = data.headers.get("location");
    let comment_id_match = comment_id.match(patt);
    return +comment_id_match[0];
  }

  create(form) {
    if(form.value.psw == form.value.confirm_psw) {
      form.value.psw = md5(form.value.psw);
      form.value.confirm_psw = md5(form.value.confirm_psw);
      let subscription = this.service.create("accounts", form.value).subscribe(
        data => {
          this.messageService.setMessage("success");
          this.account_id = this.getLocationHeaders(data);
          // form.reset();
        },
        err => {
          console.log(err);
          this.messageService.setMessage("rejected");
          this.loading = false;
        },
        () => {
          let settings = {
            account_id: this.account_id
          }
          this.service.create("profile_settings", settings).subscribe(
            data => {
              this.profile_settings_id = this.getLocationHeaders(data);
              this.navigation.redirect("/accounts");
            },
            err => {
              console.log(err);
            }
          )
        }
      );

      this.sub.add(subscription);
    } else {
      this.messageService.setMessage("confirm_incorrect_psw");
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



