import { Component, OnInit, OnDestroy, AfterViewInit, ElementRef, ViewChild } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { max } from 'date-fns';
import { Subscription } from "rxjs/Subscription";
declare var $:any;

// Services
import { Service } from '../../../services/http.service';
import { MessageService } from '../../../services/message.service';
import { AccountService } from '../../accounts/service/account.service';
import { EventSourceListenerService } from '../../../services/event-source-listener.service';

// Interfaces
import { Account } from '../../accounts/interface/account';
import { Comment } from '../comments/interface/comment';
import { Ticket } from '../interface/ticket';

// Helpers
import { Timezone } from '../../../helpers/timezone';
import { LocalStorageService } from '../../../helpers/local-storage';
import { NavigationService } from '../../../helpers/navigation';

@Component({
  templateUrl: './details.component.html',
  styleUrls: ['./details.component.scss']
})
export class TicketDetailsComponent implements OnInit, AfterViewInit, OnDestroy {

  updated_at: string;
  created_at: string;
  deadline_at: number;

  updated_by: any = {};

  ticket_id: number;
  ticket: any = {};
  account: Account;
  accounts: Account[];
  comments: Comment[];

  sub = new Subscription();

  @ViewChild("buttons", {read: ElementRef}) buttons: ElementRef;
  @ViewChild("description", {read: ElementRef}) description: ElementRef;

  constructor(
    private service: Service,
    public route: ActivatedRoute,
    public timezone: Timezone,
    private messageService: MessageService,
    private lsService: LocalStorageService,
    private accountService: AccountService,
    private navigation: NavigationService,
    private eventSourceListenerService: EventSourceListenerService
  ) {
    // Get account's details by local storage
    this.account = JSON.parse(this.lsService.get("account"));

    // Download account's list
    this.getAccounts();

    // Get id current ticket
    let subscription = this.route.params.subscribe(
      params => {
        this.ticket_id = +params['id'];
      }
    );
    this.sub.add(subscription);
  }

  getTicket(ticket_id) {
    let query = "id=eq." + ticket_id + "&select=*, reporter: accounts(id, firstname, lastname)";
    let subscription = this.service.getRecord("tickets", query).subscribe(
      (data: any) => {
        if(this.doYouHavePermission(data.assignees_id, this.account.id, this.account.profile, data.created_by)) {
          this.ticket = data;
          this.getAssignees();
          if(this.ticket.expire_at) {
            this.deadline_at = this.timezone.daysLeftToDeadline(new Date(), this.ticket.expire_at);
          }
          this.created_at = this.timezone.getDistanceInWordsToNow(this.ticket.created_at);
          this.updated_at = this.timezone.getDistanceInWordsToNow(this.ticket.updated_at);
          this.updated_by = this.lastChangeRunBy(this.ticket.updated_by);
        }
      },
      err => {
        console.log(err)
      },
      () => {
        this.setReadBy();
        this.setStatusAsRead();
      }
    );

    this.sub.add(subscription);
  }

  getAccounts() {
    let query = "deleted=is.false" + "&status=eq." + 1;
    // let query = "deleted=is.false" + "&status=eq." + 1 + "&id=neq." + this.account.id;
    let subscription = this.service.getRecords("accounts", query).subscribe(
      data => {
        this.accounts = data;
      }
    );
    this.sub.add(subscription);
  }

  getComments() {
    let query = "ticket_id=eq." + this.ticket_id + "&order=" + "id.desc" + "&select=*, account: accounts(avatar_color, firstname, lastname, id)";
    let subscription = this.service.getRecords("comments", query).subscribe(
      data => {
        this.comments = data;
      },
      err => {
        console.log(err);
      }
    );

    this.sub.add(subscription);
  }

  getAssignees() {
    let request_query = "id=in." + "(" + this.ticket.assignees_id.toString() + ")" + "&deleted=is.false" + "&status=eq.1" + "&select=id,firstname,lastname";
    //let request_query = "id=in." + "(" + this.ticket.assignees_id.toString() + ")" + "&select=id,firstname,lastname";
    let subscription = this.service.getRecords("accounts", request_query).subscribe(
      data => {
        this.ticket.assignees = data;
      },
      err => {
        console.log(err)
      }
    );

    this.sub.add(subscription);
  }

  updateIt(form) {
    let query;
    let form_copy = Object.assign({}, form.value);
    if(this.ticket.updated_at != null) {
      query = "id=eq." + this.ticket.id + "&" + "updated_at=eq." + this.ticket.updated_at;
    } else {
      query = "id=eq." + this.ticket.id
    }

    form_copy.updated_by = this.account.id;
    form_copy.updated_at = this.timezone.getCurrentDate();
    form_copy.read_by = '{}';
    this.ticket.read_by = [];
    let subscription = this.service.update("tickets", query, form_copy).subscribe(
      data => {
        this.messageService.setMessage('success');

        this.updated_at = this.timezone.getDistanceInWordsToNow(new Date());
        this.ticket.updated_at = form_copy.updated_at;

        this.updated_by = this.account;

        this.hiddenButtons();
      },
      err => {
        console.log(err);
        this.messageService.setMessage('rejected');
      }
    );

    this.sub.add(subscription);
  }

  deleteIt() {
    let subscription = this.service.delete("tickets", "id", this.ticket_id).subscribe(
      data => {
        this.messageService.setMessage('delete');
        this.navigation.redirect("/tickets");
      },
      err => {
        this.messageService.setMessage('rejected');
        console.log(err)
      }
    );

    this.sub.add(subscription);
  }

  archiveIt() {
    let obj = {
      "archived": true,
      "updated_by": this.account.id,
      "updated_at": this.timezone.getCurrentDate()
    };
    let subscription = this.service.archived("tickets", this.ticket_id, obj).subscribe(
      data => {
        this.messageService.setMessage('archive');
        this.navigation.redirect("/tickets");
      },
      err => {
        this.messageService.setMessage('rejected');
        console.log(err)
      }
    );

    this.sub.add(subscription);
  }

  revokeIt(){
    this.hiddenButtons();
    this.getTicket(this.ticket_id);
  }

  onSelectionChange(form) {
    let form_copy = Object.assign({}, form.value);
    //let form_copy: any = new Object(form.value);
    delete form_copy['assignee_id'];
    form_copy.updated_by = this.account.id;
    form_copy.updated_at = this.timezone.getCurrentDate();
    form_copy.read_by = '{}';
    this.ticket.read_by = [];
    let query = "id=eq." + this.ticket.id;
    // if ticket status is equal to 'rigettato' or 'terminato' archive it
    // if(form_copy.status == 3 || form_copy.status == 4) {
    //   form_copy.archived = true;
    // } else {
    //   console.log("diverso da terminato e rigettato");
    // }
    //console.log(form_copy);
    let subscription = this.service.update("tickets", query, form_copy).subscribe(
      data => {
        //console.log("OK");
        this.messageService.setMessage('success');
        this.ticket.read_by = [];
        this.updated_by = this.account;
        this.updated_at = this.timezone.getDistanceInWordsToNow(this.timezone.getCurrentDate());
        //console.log("fine");
        this.navigation.redirect('/tickets');
      },
      err => {
        console.log(err);
        this.messageService.setMessage('rejected');
      }
    );

    this.sub.add(subscription);
  }

  lastChangeRunBy(id) {
    let account = this.accounts.find(function(account) {
      return id === account.id
    });
    return account
  }

  hiddenButtons() {
    let buttons = this.buttons.nativeElement;
    buttons.style.display = 'none';
  }

  showButtons() {
    let buttons = this.buttons.nativeElement;
    buttons.style.display = 'block';
  }

  doYouHavePermission(assignees_id, logged_accound_id, logged_accound_profile, created_by) {
    if(assignees_id.includes(logged_accound_id) || created_by == logged_accound_id || logged_accound_profile == 0) {
      return true
    } else {
      this.messageService.setMessage('without_permissions');
      this.navigation.redirect('/');
    }
  }


  split(data) {
    let str = data.split(";");
    let obj = {
      "id": +str[0],
      "firstname": str[1],
      "lastname": str[2]
    };
    return obj
  }

  checkIfAlreadySelected(data) {
    return this.ticket.assignees_id.includes(data);
  }

  addAssignee(data){
    let assigneeSelected = this.split(data.value);

    // Request
    let query_ticket;
    if(this.ticket.updated_at != null) {
      query_ticket = "id=eq." + this.ticket.id + "&" + "updated_at=eq." + this.ticket.updated_at;
    } else {
      query_ticket = "id=eq." + this.ticket.id
    }
    // end

    // Check if account has been already assignated to
    let resultCheck = this.checkIfAlreadySelected(assigneeSelected.id);
    if(resultCheck != true) {
      this.ticket.assignees_id.push(assigneeSelected.id);
      // Create obj to send
      let update_ticket = {
        "updated_by": this.account.id,
        "updated_at": this.timezone.getCurrentDate(),
        "assignees_id": "{" + this.ticket.assignees_id.join(",") + "}",
        "read_by": "{}"
      }
      // end

      let subscription = this.service.update("tickets", query_ticket, update_ticket).subscribe(
        data => {
          this.messageService.setMessage('success');
          // Get current Date in words
          this.updated_at = this.timezone.getDistanceInWordsToNow(new Date());
          // end
          this.ticket.updated_at = update_ticket.updated_at;
          this.ticket.assignees.push(assigneeSelected);
          this.ticket.read_by = [];
        },
        err => {
          this.messageService.setMessage('overwrite');
          console.log(err)
        }
      );

      this.sub.add(subscription);
    } else {
      this.messageService.setMessage('prohibition');
    }
    // end

  }

  setStatusAsRead() {
    let query = "id=eq." + this.ticket_id;
    let status_set = {
      "status": 6
    }
    if(this.account.id != this.ticket.created_by && this.ticket.status == 5) {
      //console.log("entrato qui riga 331 dettagli.html");
      this.ticket.status = 6;
      let subscription = this.service.update("tickets", query, status_set).subscribe();

      this.sub.add(subscription);
    }
  }

  setReadBy() {
    //console.log("eseguita");
    let query = "id=eq." + this.ticket_id;
    let accounts = this.ticket.read_by;
    if(!accounts.includes(this.account.id)) {
      this.ticket.read_by.push(this.account.id);
      let obj = {
        "read_by": "{" + this.ticket.read_by.join(",") + "}"
      };
      let subscription = this.service.update("tickets", query, obj).subscribe();
      this.sub.add(subscription);
    }
  }

  ngOnInit() {
    //console.log("OnInit");
    this.getTicket(this.ticket_id);
    this.getComments();
  }

  ngAfterViewInit() {
    //console.log("afterviewinit");
    //console.log(this.ticket);
    let _self = this;
    this.hiddenButtons();
    $(this.description.nativeElement).on('click', function (e, editor) {
      _self.showButtons();
    });

    // In ascolto
    let sseListener = this.eventSourceListenerService.item$.subscribe(
      data => {
        if(data.length > 0) {
          data.map(function(elem) {
            if(elem.table == "comments" || elem.table == "tickets") {
              //console.log("entrato nell if");
              _self.getTicket(_self.ticket_id);
              _self.getComments();
              return true
            }
          });
        }
      }
    );
    this.sub.add(sseListener);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



