import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { Subscription } from "rxjs/Subscription";
declare var $:any;

// Services
import { Service } from '../../../../services/http.service';

// Interface
import { Note } from '../../../notes/interface/note';
import { Account } from '../../../accounts/interface/account';

@Component({
  selector: 'card-note-list',
  templateUrl: './note-list.component.html',
  styleUrls: ['./note-list.component.scss']
})
export class CardNoteListComponent implements OnInit, OnDestroy {

  @Input() logged_account: Account;
  notes: Note[] = [];

  sub = new Subscription();

  constructor(
    private service: Service
  ) {}

  getNotes() {
    let date = new Date();
    let gte_date = date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate();
    let lte_date = (date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + (date.getDate() + 1));

    let query = "and=(" + "alarm_date.gte." + gte_date + "," + "alarm_date.lte." + lte_date + ")" + "&alarm_date=not.is.null" + "&account_id=eq." + this.logged_account.id + "&archived=" + "is.false";

    //let query = "account_id=eq." + this.logged_account.id + "&archived=" + "is.false" + "&limit=20";
    let subscription = this.service.getRecords("notes", query).subscribe(
      data => {
        this.notes = data;
      },
      err => {
        console.log(err);
      }
    );

    this.sub.add(subscription);
  }

  ngOnInit() {
    this.getNotes();
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



