import { Component, OnInit, Input } from '@angular/core';
import { max } from 'date-fns';

// Services
import { Timezone } from '../../../../../helpers/timezone';

@Component({
  selector: 'dashboard-ticket-list-row',
  templateUrl: './row.component.html',
  styleUrls: ['./row.component.scss']
})
export class DashboardTicketListRowComponent implements OnInit {

  @Input() ticket;
  deadline_at;

  constructor(
    private timezone: Timezone
  ) {}

  daysLeftDeadline(expire_at) {
    const current_date = new Date(),
          expire_date = new Date(expire_at);

    let latest_date = max(
        current_date,
        expire_date
    );
    return this.timezone.getDifferenceInCalendarDays(latest_date, current_date)
  }

  ngOnInit() {
    this.deadline_at = this.daysLeftDeadline(this.ticket.expire_at);
  }

}



