import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../../services/http.service';

// Interface
import { Ticket } from '../../../tickets/interface/ticket';
import { Account } from '../../../accounts/interface/account';

@Component({
  selector: 'card-ticket-list',
  templateUrl: './ticket-list.component.html',
  styleUrls: ['./ticket-list.component.scss']
})
export class CardTicketListComponent implements OnInit, OnDestroy {

  @Input() logged_account: Account;
  tickets: Ticket[] = [];

  sub = new Subscription();

  constructor(
    private service: Service
  ) {}

  getTickets() {
    let date = new Date();
    let gte_date = date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate();
    let lte_date = (date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + (date.getDate() + 1));
    // let query = "assignees_id=cs." + "{" + this.logged_account.id + "}" + "&" + "archived=" + "is.false" + "&" + "status=neq." + "3" + "&limit=20" + "&select=*, reporter: accounts(firstname, lastname)";
    let query = "and=(" + "expire_at.gte." + gte_date + "," + "expire_at.lte." + lte_date + ")" + "&expire_at=not.is.null" + "&assignees_id=cs." + "{" + this.logged_account.id + "}" + "&" + "archived=" + "is.false" + "&" + "status=neq." + "3" + "&select=*, reporter: accounts(firstname, lastname)";
    let subscription = this.service.getRecords("tickets", query).subscribe(
      data => {
        this.tickets = data;
      },
      err => {
        console.log(err);
      }
    );

    this.sub.add(subscription);
  }

  ngOnInit() {
    this.getTickets();
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



