import {Injectable} from "@angular/core";
import { distanceInWordsToNow, getTime, getMonth, getYear, getDate, differenceInCalendarDays, isSameDay, max } from 'date-fns';
import * as itLocale from 'date-fns/locale/it/index.js';

@Injectable()
export class Timezone {

  constructor() {}

  getCurrentDate() {
    let date = new Date();
    let isoDate = new Date(date.getTime() - (date.getTimezoneOffset() * 60000)).toISOString();
    return isoDate;
  }

  getDistanceInWordsToNow(date) {
    //console.log(date);
    return distanceInWordsToNow(date, {locale: itLocale, includeSeconds: true});
  }

  getDifferenceInCalendarDays(first_date, second_date) {
    let first_date_year = getYear(first_date),
        first_date_month = getMonth(first_date) + 1,
        first_date_day = getDate(first_date),
        second_date_year = getYear(second_date),
        second_date_month = getMonth(second_date) + 1,
        second_date_day = getDate(second_date);
    return differenceInCalendarDays(
      new Date(first_date_year, first_date_month, first_date_day, 0, 0),
      new Date(second_date_year, second_date_month, second_date_day, 0, 0)
    )
  }

  checkIfSameDay(first_date, second_date) {
    return isSameDay(
      first_date,
      second_date
    );
  }

  splitTimeZoneDate(current_date) {
    return current_date.split("T")[0];
  }

  daysLeftToDeadline(begin_at, end_at) {
    const current_date = begin_at,
          expire_date = end_at;

    let latest_date = max(
        current_date,
        expire_date
    );
    return this.getDifferenceInCalendarDays(latest_date, current_date)
  }

}



