import { Component, OnDestroy, Input } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../services/http.service';
import { MessageService } from '../../../services/message.service';

// Helpers
import { Timezone } from '../../../helpers/timezone'

@Component({
  selector: 'shared-documents-with',
  templateUrl: './shared-documents-with.component.html',
  styleUrls: ['./shared-documents-with.component.scss']
})
export class SharedDocumentsWithComponent implements OnDestroy {

  @Input() document;
  @Input() account_id;

  sub = new Subscription();

  constructor(
    private service: Service,
    public timezone: Timezone,
    private messageService: MessageService
  ) { }

  removeIt(account) {
    // Request
    let query;
    if(this.document.updated_at != null) {
      query = "id=eq." + this.document.id + "&" + "updated_at=eq." + this.document.updated_at;
    } else {
      query = "id=eq." + this.document.id
    }
    // end

    // Remove assigne id from model for assignees list
    this.document.share_with.splice(this.document.share_with.indexOf(account.id), 1);
    // end

    // Create obj to send for upload
    let upload_document = {
      "updated_by": this.account_id,
      "updated_at": this.timezone.getCurrentDate(),
      "share_with": "{" + this.document.share_with.join(",") + "}"
    }
    // end

    let subscription = this.service.update("documents", query, upload_document).subscribe(
      data => {
        this.messageService.setMessage('success');
        this.document.updated_at = upload_document.updated_at;
        // Remove assignee selected from view
        this.document.accounts.splice(this.document.accounts.indexOf(account), 1);
        // end
      },
      err => {
        this.messageService.setMessage('overwrite');
        console.log(err);
      }
    );

    this.sub.add(subscription);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



