import { Component, OnDestroy, Input } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../../services/http.service';
import { MessageService } from '../../../../services/message.service';

// Interface
import { Addressbook } from '../../interface/addressbook';

@Component({
  selector: '[archived-addressbook-row]',
  templateUrl: './row.component.html',
  styleUrls: ['./row.component.scss']
})
export class AddressbookArchivedRowComponent implements OnDestroy {

  @Input() addressbook: Addressbook;
  @Input() addressesbook: Addressbook[];

  sub = new Subscription();

  constructor(
    private service: Service,
    private messageService: MessageService
  ) { }

  rollbackIt() {
    let index = this.addressesbook.indexOf(this.addressbook);
    let archived = {
      "archived": false
    };
    let subscription = this.service.archived("addressbook", this.addressbook.id, archived).subscribe(
      data => {
        this.addressesbook.splice(index, 1);
        this.messageService.setMessage('rollback');
      },
      err => {
        console.log(err)
      }
    );

    this.sub.add(subscription);
  }

  deleteIt() {
    let index = this.addressesbook.indexOf(this.addressbook);
    let subscription = this.service.delete("addressbook", "id", this.addressbook.id).subscribe(
      data => {
        this.addressesbook.splice(index, 1);
        this.messageService.setMessage('delete');
      },
      err => {
        console.log(err)
      }
    );

    this.sub.add(subscription);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



