import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Subject } from 'rxjs/Subject';

@Injectable()
export class MessageService {

  private subject = new Subject<any>();

  setMessage(type: string) {
    let message = this.switchMessage(type);
    this.subject.next(message);
    setTimeout(function() {
      this.clearMessage();
    }.bind(this), 3000);
  }

  clearMessage() {
    this.subject.next({});
  }

  getMessage(): Observable<any> {
    return this.subject.asObservable();
  }

  switchMessage(type) {
    let message = {"success": 1, "delete": 2, "rejected": 3, "prohibition": 4, "archive": 5, "rollback": 6, "access-denied": 7, "psw-rejected": 8, "overwrite": 9, "account_in_use": 10, "confirm_incorrect_psw": 11, "without_permissions": 12};
    switch(message[type]) {
      case 1:
        return {
          "text": "L'operazione è andata a buon fine!",
          "class": "success",
          "display": true
        };
      case 2:
        return {
          "text": "Cancellato con successo!",
          "class": "success",
          "display": true
        };
      case 3:
        return {
          "text": "Si è verificato un errore",
          "class": "danger",
          "display": true
        };
      case 4:
        return {
          "text": "Non puoi aggiungerlo poichè è già presente!",
          "class": "danger",
          "display": true
        };
      case 5:
        return {
          "text": "Archiviato con successo! NOTA: Hai la possibilità di ripristinarlo!",
          "class": "success",
          "display": true
        };
      case 6:
        return {
          "text": "Ripristinato con successo!",
          "class": "success",
          "display": true
        };
      case 7:
        return {
          "text": "Accesso non consentito. Email o Password non validi",
          "class": "danger",
          "display": true
        };
      case 8:
        return {
          "text": "La password corrente da te inserita non corrisponde",
          "class": "danger",
          "display": true
        };
      case 9:
        return {
          "text": "Al momento non è possibile salvare perchè un altro utente ha effettuato delle modifiche",
          "class": "danger",
          "display": true
        };
      case 10:
        return {
          "text": "Non è possibile archiviare l'account perché è attualmente collegato a dei tickets",
          "class": "danger",
          "display": true
        };
      case 11:
        return {
          "text": "Non ti è consentito anadare avanti perché la password di conferma inserita non corrisponde alla password",
          "class": "danger",
          "display": true
        };
      case 12:
        return {
          "text": "Accesso non consentito, non hai i permessi.",
          "class": "danger",
          "display": true
        };
    }
  }

}



