import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable()
export class NoteService {

  notes = new BehaviorSubject<any>([]);

  current_day_notes = new BehaviorSubject<any>([]);

  set(list) {
    this.notes.next(list);
  }

  set setCurrentDayNotes(list) {
    this.current_day_notes.next(list);
  }

  get getCurrentDayNotes(): Observable<any> {
    return this.current_day_notes.asObservable();
  }

}



