import { Component, OnInit, Input } from '@angular/core';

// Services
import { AccountService } from '../../../accounts/service/account.service';

@Component({
  selector: 'profile-main-nav',
  templateUrl: './profile-main-nav.component.html',
  styleUrls: ['./profile-main-nav.component.scss']
})
export class ProfileMainNavComponent implements OnInit {

  @Input() account: Object;

  constructor(
    private accountService: AccountService
  ) { }

  logout() {
    this.accountService.logout(this.account);
  }

  ngOnInit() {}

}



