import {Injectable} from "@angular/core";
import { Subject, BehaviorSubject, Observable } from 'rxjs';

// Services
import { Service } from '../../../services/http.service';

// Helpers
import { LocalStorageService } from '../../../helpers/local-storage';
import { NavigationService } from '../../../helpers/navigation';

// Interfaces
import { Account } from '../interface/account';

@Injectable()
export class AccountService {

  logged_account: Subject<any> = new BehaviorSubject<any>(null);

  constructor(
    private service: Service,
    private lsService: LocalStorageService,
    private navigation: NavigationService
  ) {}

  public getLoggedAccount(): Observable<any> {
    return this.logged_account.asObservable();
  }

  public setLoggedAccount(account: any): void {
    this.logged_account.next(account);
    this.lsService.set([
      {
        'key': 'account',
        'value': JSON.stringify(account)
      }
    ]);
  }

  setOnlineStatus(account, status) {
    let query = "id=eq." + account.id;
    let online = {
      online: status
    }
    this.service.update("accounts", query, online).subscribe(
      data => {
        console.log("ok");
      },
      err => {
        console.log(err);
      }
    );
  }

  setLocalStorage(account, expire_date) {
    this.lsService.set([
      {
        'key': 'account',
        'value': JSON.stringify(account)
      },
      {
        'key': 'accountId',
        'value': account.id
      },
      {
        'key': 'accountProfile',
        'value': account.profile
      },
      {
        'key': 'expires',
        'value': (expire_date).toString()
      }
    ]);
    this.navigation.redirect('/');
  }

  logout(account) {
    // remove user from local storage to log user out
    this.setOnlineStatus(account, false);
    this.lsService.reset([
      'expires', 'accountId', 'accountProfile', 'account'
    ]);
    this.clean();
    this.navigation.redirect('/signin');
  }

  clean() {
    this.logged_account.next(null);
  }

}



