import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { take } from 'rxjs/operators';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

// Helpers
import { LocalStorageService } from '../helpers/local-storage';

@Injectable()
export class EventSourceListenerService {

  list: BehaviorSubject<any[]> = new BehaviorSubject<any>([]);
  item$: Observable<any> =  this.list.asObservable();
  account_id: number;
  account: any;
  url: string = "";

  status:boolean = true;

  constructor(
    private lsService: LocalStorageService
  ) {
    this.account = JSON.parse(this.lsService.get('account'));
    this.account_id = +this.lsService.get('accountId');

    this.url = 'http://' + window.location.hostname + ':4001/bracco/' + this.account_id + "/events";
  }

  flashingTab() {
    let toggle_status = true;
    let interval = setInterval(function(){
      if(toggle_status) {
        document.title = "C'è una notifica per te";
      } else {
        document.title = "Bracco";
      }
      toggle_status = !toggle_status;
    }, 1000);
    return interval
  }

  stopFlashingTab(interval) {
    document.addEventListener("click", function() {
      clearInterval(interval);
      document.title = "Bracco";
    });
  }

  soundNotifications() {
    let audioPlayer = <HTMLVideoElement> document.getElementById("notifications");
    audioPlayer.play();
  }

  connect(): void {
    const source = new window['EventSource'](this.url);
    source.addEventListener('message', event => {
      // Show sound and flash notifications if user say yes
      if(this.account.settings[0].tickets_notifications === true) {
        if(this.account.settings[0].tickets_sound_notifications === true) {
          this.soundNotifications();
        }

        if(this.account.settings[0].tickets_flash_notifications === true) {
          if(!document.hasFocus() || document.visibilityState == "hidden") {
            alert("C'è una notifica per te");
            let interval = this.flashingTab();
            this.stopFlashingTab(interval);
          }
        }
      }
      // end

      let modified_object = this.editObject(JSON.parse(event.data));

      if(this.isItIntoObservableArray(modified_object)) {
        this.updateElement(modified_object);
      } else {
        this.addElementToObservableArray(modified_object);
      }
    });
    source.addEventListener('open', event => {
      console.log("eventsource connected.")
			//addStatus('eventsource connected.')
		});
    source.addEventListener('error', event => {
			// if (event.eventPhase == EventSource.CLOSED) {
      //   console.log("eventsource was closed.")
			// }
      console.log("eventsource was closed.")
		});
  }

  editObject(data) {
    return {
      "id": data.message.record.id,
      "title": data.message.record.title,
      "ticket_id": data.message.table == "tickets" ? data.message.record.id : data.message.record.ticket_id,
      "operation": data.message.operation,
      "table": data.message.table,
      "text": this.switchMessage(
        data.message.table == "tickets" ? data.message.record.id : data.message.record.ticket_id,
        data.message.table == "tickets" ? data.message.record.title : "",
        data.message.operation,
        data.message.table
      ),
      "read": false
    };
  }

  switchMessage(id, title, operation, table) {
    let casestudies = {"DELETE": 1, "ARCHIVE": 2, "INSERT": 3, "UPDATE": 4};
    switch(casestudies[operation]) {
      case 1: {
        if(table == 'tickets') {
          return "Il ticket " + "'" + title  + "'" + " è stato cancellato."
        } else if(table == 'comments') {
          return "Al ticket " + "'" + title  + "'" + " è stato eliminato un commento."
        } else if(table == 'documents') {
          return "Il document DOCUMENT-" + id + " è stato cancellato."
        }
      };
      case 2: {
        if(table == 'tickets') {
          return "Il ticket " + "'" + title  + "'" + " è stato archiviato."
        } else if(table == 'documents') {
          return "Il documento DOCUMNT-" + id + " è stato archiviato."
        }
      };
      case 3: {
        if(table == 'tickets') {
          return "Ti è stato assegnato un nuovo ticket"
        } else if(table == 'comments') {
          return "E' stato inserito un nuovo commento al ticket TICKET-" + id
        }
      };
      case 4: {
        if(table == 'tickets') {
          return "Il ticket " + "'" + title  + "'" + " è stato modificato."
        } else if(table == 'comments') {
          return "Al ticket " + "'" + title  + "'" + " è stato modificato un commento."
        } else if(table == 'documents') {
          return "Il documento DOCUMENT-" + id + " è stato modificato."
        }
      };
    }
  }

  addElementToObservableArray(item) {
    this.item$.pipe(take(1)).subscribe(val => {
      const newArr = [...val, item];
      this.list.next(newArr);
    });
  }

  removeElementFromObservableArray(item) {
    //console.log(item);
    this.item$.pipe(take(1)).subscribe(val => {
      let index = val.findIndex(function(elem) {
        //console.log(elem.message.record.id == item.id);
        return elem.id == item.id
      });
      val.splice(index, 1);
      this.list.next(val);
    });
  }

  updateElement(item) {
    //console.log(item);
    this.item$.pipe(take(1)).subscribe(val => {
      let index = val.findIndex(function(elem) {
        return elem.id == item.id
      });
      val[index] = item;
      this.list.next(val);
    });
  }

  isItIntoObservableArray(item) {
    let result: boolean;
    this.item$.pipe(take(1)).subscribe(val => {
      result = val.some(function(elem) {
        return elem.id == item.id
      });
    });
    if(result) {
      return true
    } else {
      return false
    }
  }

  clearData() {
    this.list.next([]);
  }

}



