import { Component, OnInit, OnDestroy, Input, ViewChild, ElementRef } from '@angular/core';
import { Subscription } from "rxjs/Subscription";
declare var $:any;

// Services
import { CommentService } from '../service/comment.service';

@Component({
  selector: 'ticket-comments-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class TicketCommentsListComponent implements OnInit, OnDestroy {

  @Input() account;
  @Input() ticket_id: number;
  @Input() comments;

  @ViewChild("buttonShowComments", {read: ElementRef}) buttonShowComments: ElementRef;

  sub = new Subscription();

  constructor(
    private commentService: CommentService
  ) { }

  showAll() {
    let list = $(".ticket-comments");
    $(list).toggleClass("show");
    if($(".ticket-comments").hasClass("show")) {
      this.buttonShowComments.nativeElement.textContent = "Nascondi storico";
    } else {
      this.buttonShowComments.nativeElement.textContent = "Mostra storico";
    }
  }

  ngOnInit() {
    let subscription = this.commentService.comment.subscribe(
      res => {
        this.comments = res;
      }
    );

    this.sub.add(subscription);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



