import { Component, OnInit, OnDestroy, Input, Output, EventEmitter } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../services/http.service';

// Helpers
import { SessionStorageService } from '../../../helpers/session-storage';
import { RequestUrl } from '../../../helpers/build-request-url';
import { CheckObject } from '../../../helpers/check-object';
import { Timezone } from '../../../helpers/timezone';

// Interface
import { Ticket } from '../interface/ticket';
import { Account } from '../../accounts/interface/account';

@Component({
  selector: 'filter-form',
  templateUrl: './filter-form.component.html',
  styleUrls: ['./filter-form.component.scss']
})
export class FilterFormComponent implements OnInit, OnDestroy {

  @Input() filter_store: boolean;
  @Output() list = new EventEmitter();
  @Output() page = new EventEmitter();
  @Input() account_profile: number;
  @Input() account_id: number;
  //@Input() tickets: Ticket[] = [];
  @Input() archived: boolean;
  accounts: Account[] = [];
  filter_settings: any = {
    "created_by": "",
    "assignees_id": "",
    "status": ""
  }

  sub = new Subscription();

  constructor(
    private service: Service,
    public buildUrl: RequestUrl,
    public checkObject: CheckObject,
    private timezone: Timezone,
    private ssService: SessionStorageService
  ) {
    this.getAccounts();
  }

  resetFilter(data) {
    let query: string = "or=(" + "and(" + "created_by.eq." + this.account_id + "," + "assignees_id.not.cs." + "{" + this.account_id + "})" + "," + "and(" + "assignees_id.cs." + "{" + this.account_id + "}" + "," + "status.neq." + 3 + "," + "status.neq." + 4 + ")" + ")" + "&archived=is." + this.archived;
    if(this.archived === true) {
      query += "&order=" + "id.desc";
    } else {
      query += "&order=" + "commented_at.desc";
    }
    query += "&select=*, reporter: accounts(id, firstname, lastname)";
    data.reset();
    if(this.filter_store) {
      this.ssService.reset([
        'filter', 'filter_page_number'
      ]);
    }
    //console.log(query);
    this.sendRequest(query);
  }

  setFilterInStorage(data) {
    let return_not_empty_params = this.checkObject.getNotEmptyPropertiesValue(data.value);
    if(this.filter_store) {
      this.ssService.set([
        {
          'key': 'filter',
          'value': JSON.stringify(return_not_empty_params)
        }
      ]);
      this.ssService.reset([
        'filter_page_number'
      ]);
    };
    this.sendRequest(this.urlComposition(return_not_empty_params));
  }

  urlComposition(data) {
    let query: string = "archived=" + "is." + this.archived;
    if(!this.checkObject.isBlank(data)) {
      if(data["assignees_id"] !== "all" && data["assignees_id"] !== undefined) {
        let copy = Object.assign({}, data);
        if(data["created_by"] == "all") {
          delete copy["created_by"];
          query += "&" + this.buildUrl.build(copy);
        } else {
          query += "&" + this.buildUrl.build(data);
        }
        if(this.account_profile != 0) {
          if(data["assignees_id"] != this.account_id) {
            query += "&" + "created_by=eq." + this.account_id;
          } else {
            if(data["status"] == undefined) {
              query += "&and=(" + "status.neq." + 3 + "," + "status.neq." + 4 + ")";
            }
          }
        }
      } else if(data["created_by"] !== "all" && data["created_by"] !== undefined) {
        let copy = Object.assign({}, data);
        if(data["assignees_id"] == "all") {
          delete copy["assignees_id"];
          query += "&" + this.buildUrl.build(copy);
        } else {
          query += "&" + this.buildUrl.build(data);
        }
      } else {
        if(data["status"]) {
          let obj = {
            "status": data["status"]
          }
          query += "&" + this.buildUrl.build(obj);
        };
        if(this.account_profile != 0) {
          query += "&" + "created_by=eq." + this.account_id;
        }
      }
    }
    query += "&order=" + "commented_at.desc" + "&select=*, reporter: accounts(id, firstname, lastname)";
    return query;
  }

  sendRequest(query) {
    let _self = this;
    let subscription = this.service.getRecords("tickets", query).subscribe(
      data => {
        let tickets = data.map(function(ticket) {
          ticket.accounts = ticket.assignees_id.map(function(assignee_id) {
            return _self.accounts.find(function(account) {return assignee_id === account.id});
          }).filter(function(elem) {
            return elem !== undefined;
          });
          if(ticket.expire_at) {
            ticket.deadline_at = _self.timezone.daysLeftToDeadline(new Date(), ticket.expire_at);
          }
          return ticket;
        });
        this.list.emit(tickets);
        this.page.emit(1);
      },
      err => {
        console.log(err)
      }
    );

    this.sub.add(subscription);
  }

  getAccounts() {
    let query = "deleted=" + "is.false" + "&status=" + "eq.1" + "&order=" + "id.desc" + "&select=id, firstname, lastname, avatar_color";
    let subscription = this.service.getRecords("accounts", query).subscribe(
      data => {
        this.accounts = data;
      }
    );

    this.sub.add(subscription);
  }

  ngOnInit() {}

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



