import { Component, OnInit, OnDestroy, ViewChild, HostListener } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { Subscription } from "rxjs/Subscription";
import { FormCanDeactivate } from '../../../helpers/form-can-deactivate/form-can-deactivate';
import { NgForm } from "@angular/forms";

// Services
import { Service } from '../../../services/http.service';
import { MessageService } from '../../../services/message.service';
import { NoteService } from '../../notes/service/note.service';
import { NotificationService } from '../../widgets/dropdown-icon-nav/service/notification.service';

// Interfaces
import { Note } from '../interface/note';

// Helpers
import { LocalStorageService } from '../../../helpers/local-storage';
import { NavigationService } from '../../../helpers/navigation';
import { Timezone } from '../../../helpers/timezone';

@Component({
  templateUrl: './details.component.html',
  styleUrls: ['./details.component.scss']
})
export class NotesDetailsComponent extends FormCanDeactivate {

  logged_account_id: number;
  id: number;
  note: any = {};
  notes: Note[] = [];
  updated_at: string;

  sub = new Subscription();

  @ViewChild('form')
  form: NgForm;

  constructor(
    private service: Service,
    private noteService: NoteService,
    public route: ActivatedRoute,
    private messageService: MessageService,
    private lsService: LocalStorageService,
    private navigation: NavigationService,
    private timezone: Timezone,
    private notificationService: NotificationService
  ) {
    super();
    this.logged_account_id = +this.lsService.get("accountId");
  }

  update() {
    let query = "id=eq." + this.note.id;
    this.note.read = false;
    this.note.updated_at = this.timezone.getCurrentDate();
    let subscription = this.service.update("notes", query, this.note).subscribe(
      data => {
        if(this.timezone.checkIfSameDay(new Date(), this.note.alarm_date)) {
          let is_there = this.notificationService.isItIntoObservableArray(this.note);
          if(is_there) {
            this.notificationService.updateElement(this.note);
          } else {
            this.notificationService.addElementToObservableArray(this.note);
          }
        }
        this.messageService.setMessage('success');
      },
      err => {
        console.log(err);
        this.messageService.setMessage('rejected');
      }
    );

    this.sub.add(subscription);
  }

  revoke(){
    this.getNote();
  }

  doYouHavePermission(account_id, logged_accound_id) {
    if(account_id == logged_accound_id) {
      return true
    } else {
      this.messageService.setMessage('without_permissions');
      this.navigation.redirect('/');
    }
  }

  getNote() {
    let subscription = this.service.getBy("notes", "id", this.id).subscribe(
      (data: any) => {
        if(this.doYouHavePermission(data.account_id, this.logged_account_id)) {
          this.note = data;
          this.updated_at = this.timezone.getDistanceInWordsToNow(this.note.updated_at);
        }
      },
      err => {
        console.log(err)
      }
    );

    this.sub.add(subscription);
  }

  ngOnInit() {
    let subNotes = this.noteService.notes.subscribe(
      res => {
        this.notes = res;
      }
    );

    let subNote = this.route.params.subscribe(params => {
      this.id = +params['id']; // (+) converts string 'id' to a number
      this.getNote();
    });

    this.sub
        .add(subNotes)
        .add(subNote);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



