import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../services/http.service';
import { AccountService } from '../../accounts/service/account.service';

// Interface
import { Note } from '../interface/note';

// Helpers
import { LocalStorageService } from '../../../helpers/local-storage';

@Component({
  selector: 'notes-archived',
  templateUrl: './archived.component.html',
  styleUrls: ['./archived.component.scss']
})
export class NotesArchivedComponent implements OnInit, OnDestroy {

  dataForRequestSearchComp: Object;
  notes: Note[] = [];
  sub = new Subscription();
  config: any;

  constructor(
    private service: Service,
    private accountService: AccountService,
    private lsService: LocalStorageService
  ) {
    this.dataForRequestSearchComp = {
      "table": "notes",
      "parameters": ["title", "description"],
      "condition": "archived=is.true" + "&account_id=eq." + +this.lsService.get("accountId")
    };

    this.config = {
      id: 'notes-archived',
			currentPage: 1,
			itemsPerPage: 10
    };
  }

  getNotes() {
    let query = "account_id=eq." + +this.lsService.get("accountId") + "&archived=" + "is.true";
    let subscription = this.service.getRecords("notes", query).subscribe(
      data => {
        this.notes = data;
      },
      err => {
        console.log(err);
      }
    );

    this.sub.add(subscription);
  }

  overWriteListResult(data) {
    this.notes = data;
    this.config.currentPage = 1;
    if(data.length < 10) {
      this.config.itemsPerPage = data.length;
    }
  }

  ngOnInit() {
    this.getNotes();
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



