import { Component, OnInit, OnDestroy, AfterViewInit } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../services/http.service';
import { AccountService } from '../../accounts/service/account.service';
import { TicketsService } from '../service/tickets.service';

// Interface
import { Ticket } from '../interface/ticket';
import { Account } from '../../accounts/interface/account';

// Helpers
import { LocalStorageService } from '../../../helpers/local-storage';

@Component({
  selector: 'tickets-archived',
  templateUrl: './archived.component.html',
  styleUrls: ['./archived.component.scss']
})
export class TicketArchivedComponent implements OnInit, OnDestroy, AfterViewInit {

  account: Account;
  accounts: Account[] = [];

  tickets: Ticket[] = [];

  sub = new Subscription();
  p: number = 1;

  constructor(
    private lsService: LocalStorageService,
    private service: Service,
    private accountService: AccountService,
    private ticketsService: TicketsService
  ) {
    this.account = JSON.parse(this.lsService.get("account"));
    this.getAccounts();
  }

  getAccounts() {
    let query = "deleted=" + "is.false" + "&status=" + "eq.1" + "&order=" + "id.desc" + "&select=id, firstname, lastname, avatar_color";
    let subscription = this.service.getRecords("accounts", query).subscribe(
      data => {
        this.accounts = data;
      }
    );

    this.sub.add(subscription);
  }

  getArchivedTickets() {
    let _self = this;
    //let query: string = "assignees_id=cs." + "{" + this.account.id + "}" + "&archived=" + "is.true" + "&order=" + "id.desc" + "&select=*, reporter: accounts(id, firstname, lastname)";
    let query: string = "or=(" + "created_by.eq." + this.account.id + "," + "assignees_id.cs." + "{" + this.account.id + "}" + ")" + "{" + this.account.id + "}" + "&archived=" + "is.true" + "&order=" + "id.desc" + "&select=*, reporter: accounts(id, firstname, lastname)";
    let subscription = this.service.getRecords("tickets", query).subscribe(
      data => {
        this.tickets = data.map(function(ticket) {
          ticket.accounts = ticket.assignees_id.map(function(assignee_id) {
            return _self.accounts.find(function(account) {return assignee_id === account.id})
          }).filter(function(elem) {
            return elem !== undefined;
          });
          return ticket;
        });
      },
      err => {
        console.log(err);
      }
    );

    this.sub.add(subscription);
  }

  ngAfterViewInit() {
    let subscription = this.ticketsService.archived_ticket.subscribe(
      res => {
        if(Object.keys(res).length > 0){
          this.tickets.unshift(res);
        }
      }
    );

    this.sub.add(subscription);
  }


  ngOnInit() {
    this.getArchivedTickets();
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



