import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { take } from 'rxjs/operators';

@Injectable()
export class NotificationService {

  list: BehaviorSubject<any[]> = new BehaviorSubject<any>([]);
  item$: Observable<any> =  this.list.asObservable();

  set(list) {
    this.list.next(list);
  }

  get(): Observable<any> {
    return this.list.asObservable();
  }

  addElementToObservableArray(item) {
    this.item$.pipe(take(1)).subscribe(val => {
      const newArr = [...val, item];
      this.list.next(newArr);
    });
  }

  removeElementFromObservableArray(item) {
    this.item$.pipe(take(1)).subscribe(val => {
      let index = val.findIndex(function(elem) {
        return elem.id == item.id
      });
      val.splice(index, 1);
      this.list.next(val);
    });
  }

  updateElement(item) {
    this.item$.pipe(take(1)).subscribe(val => {
      let index = val.findIndex(function(elem) {
        return elem.id == item.id
      });
      val[index] = item;
      this.list.next(val);
    });
  }

  isItIntoObservableArray(item) {
    let result: boolean;
    this.item$.pipe(take(1)).subscribe(val => {
      result = val.some(function(elem) {
        return elem.id == item.id
      });
    });
    if(result) {
      return true
    } else {
      return false
    }
  }

}



