import { Component, OnInit, OnDestroy, Input, Output, EventEmitter, ViewChild, ElementRef } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
declare var $:any;

// Services
import { Service } from '../../../../../services/http.service';
// import { MessageService } from '../../../../../services/message.service';
// import { AccountService } from '../../../../accounts/service/account.service';

// Helpers
import { Timezone } from '../../../../../helpers/timezone';
import { LocalStorageService } from '../../../../../helpers/local-storage';

// Interface
import { Comment } from '../../interface/comment';

@Component({
  selector: 'comment-list-row',
  templateUrl: './row.component.html',
  styleUrls: ['./row.component.scss']
})
export class CommentListRowComponent implements OnInit, OnDestroy {

  sub = new Subscription();

  @Input() comments: Comment[];
  @Input() comment;
  @Input() ticket_id: number;
  logged_account_id: number;

  comment_value: string = "";

  @ViewChild("body_text", {read: ElementRef}) body_text: ElementRef;

  constructor(
    private service: Service,
    public timezone: Timezone,
    private lsService: LocalStorageService
  ) {
    this.logged_account_id = +this.lsService.get("accountId");
  }

  editIt() {
    this.comment_value = this.body_text.nativeElement.innerText;
    this.body_text.nativeElement.contentEditable = true;
    this.body_text.nativeElement.style.minHeight = '100px';
  }

  revokeIt() {
    this.body_text.nativeElement.innerText = this.comment_value;
    this.body_text.nativeElement.contentEditable = false;
    this.body_text.nativeElement.style.minHeight = 'auto';
  }

  deleteIt() {
    const index = this.comments.indexOf(this.comment);
    let subscription = this.service.delete("comments", "id", this.comment.id).subscribe(
      data => {
        this.comments.splice(index, 1);
      }
    );

    this.sub.add(subscription);
  }

  updateIt() {
    let query = "id=eq." + this.comment.id;
    let obj = {
      body_text: this.comment.body_text,
      updated_at: this.timezone.getCurrentDate()
    }
    let subscription = this.service.update("comments", query, obj).subscribe(
      data => {
        this.comment.updated_at = this.timezone.getDistanceInWordsToNow(new Date());
        this.body_text.nativeElement.contentEditable = false;
        this.body_text.nativeElement.style.minHeight = 'auto';
      },
      err => {
        console.log(err)
      },
      () => {
        this.updateTicket();
      }
    );

    this.sub.add(subscription);
  }

  updateTicket() {
    let obj = {
      "read_by": '{}',
      "commented_at": this.timezone.getCurrentDate()
    };
    let query = "id=eq." + this.ticket_id;
    let subscription = this.service.update("tickets", query, obj).subscribe();
    this.sub.add(subscription);
  }

  ngOnInit() {
    // Set to convert db date to date type string
    if(this.comment.updated_at !== null) {
      this.comment.updated_at = this.timezone.getDistanceInWordsToNow(this.comment.updated_at);
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



