import { Component, OnInit } from '@angular/core';

// Services
import { Service } from '../../../services/http.service';
import { AccountService } from '../../accounts/service/account.service';

// Interface
import { Note } from '../interface/note';

// Helpers
import { LocalStorageService } from '../../../helpers/local-storage';

@Component({
  selector: 'notes-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class NotesListComponent implements OnInit {

  logged_account_id: number;
  dataForRequestSearchComp: Object;
  notes: Note[] = [];
  config: any;

  constructor(
    private service: Service,
    private accountService: AccountService,
    private lsService: LocalStorageService
  ) {
    this.logged_account_id = +this.lsService.get("accountId");

    this.dataForRequestSearchComp = {
      "table": "notes",
      "parameters": ["title", "description"],
      "condition": "archived=is.false" + "&account_id=eq." + this.logged_account_id
    };

    this.config = {
      id: 'notes-list',
			currentPage: 1,
			itemsPerPage: 10
    };
  }

  getNotes() {
    let query = "account_id=eq." + this.logged_account_id + "&archived=" + "is.false" + "&order=" + "updated_at.desc";
    this.service.getRecords("notes", query).subscribe(
      data => {
        this.notes = data;
      },
      err => {
        console.log(err);
      }
    );
  }

  overWriteNotes(data) {
    this.notes = data;
    this.config.currentPage = 1;
    if(data.length >= 10) {
      this.config.itemsPerPage = 10
    } else {
      this.config.itemsPerPage = data.length;
    }
  }

  ngOnInit() {
    this.getNotes();
  }

}



