import { Component, OnInit, OnDestroy, Input, ViewChild, HostListener } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { addHours } from 'date-fns';
import * as jsPDF from 'jspdf';
import { FormCanDeactivate } from '../../../helpers/form-can-deactivate/form-can-deactivate';
import { NgForm } from "@angular/forms";
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../services/http.service';
import { MessageService } from '../../../services/message.service';
import { AccountService } from '../../accounts/service/account.service';

// Interfaces
import { Account } from '../../accounts/interface/account';
import { Document } from '../interface/document';

// Helpers
import { Timezone } from '../../../helpers/timezone';
import { LocalStorageService } from '../../../helpers/local-storage';
import { NavigationService } from '../../../helpers/navigation';

@Component({
  templateUrl: './details.component.html',
  styleUrls: ['./details.component.scss']
})
export class DocumentDetailsComponent extends FormCanDeactivate {

  @ViewChild('mainInfo')
  form: NgForm;
  sub = new Subscription();

  public editor_options: Object = {
    placeholderText: 'Inserisci testo',
    charCounterCount: false,
    toolbarVisibleWithoutSelection: false,
    toolbarSticky: false,
    toolbarButtons: ['bold', 'italic', 'underline', 'strikeThrough', '|', 'fontFamily', 'fontSize', 'color', 'paragraphStyle', '|', 'paragraphFormat', 'align', 'formatOL', 'formatUL', 'outdent', 'indent', 'quote', 'insertLink', 'insertTable', '|', 'insertHR', 'selectAll', 'clearFormatting', '|', 'undo', 'redo'],
    height: 300,
    theme: "gray",
    tooltips: true
  }

  updated_at: string;
  created_at: string;

  account_id: number;
  accounts: Account[];

  document_id: number;
  document: any = {};


  constructor(
    private service: Service,
    public route: ActivatedRoute,
    public timezone: Timezone,
    private messageService: MessageService,
    private accountService: AccountService,
    private lsService: LocalStorageService,
    private navigation: NavigationService
  ) {
    super();
    this.route.params.subscribe(params => {
      this.document_id = +params['id'];
      this.getDocument(this.document_id);
    });
    this.account_id = +this.lsService.get("accountId");
  }

  generatePDF() {
    let id = this.document_id;
    let pdf = new jsPDF('p', 'pt', 'letter');
    let source = document.getElementById('document_canvas');
    let specialElementHandlers = {
      '#bypassme': function (element, renderer) {
        // true = "handled elsewhere, bypass text extraction"
        return true
      }
    };
    let margins = {
      top: 60,
      bottom: 60,
      left: 40,
      width: 522
    };
    pdf.fromHTML(
      source, // HTML string or DOM elem ref.
      margins.left, // x coord
      margins.top, { // y coord
        'width': margins.width, // max width of content on PDF
        'elementHandlers': specialElementHandlers
      },
      function (dispose) {
        pdf.save("Bracco-" + id  + ".pdf");
      }, margins
    );
  }

  revoke(){
    this.getDocument(this.document_id);
  }

  checkIfAlreadySelected(data) {
    return this.document.share_with.includes(data);
  }

  split(data) {
    let str = data.split(";");
    let obj = {
      "id": +str[0],
      "firstname": str[1],
      "lastname": str[2]
    };
    return obj
  }

  shareWith(data) {
    let accountSelected = this.split(data.value);

    // Request
    let query_document;
    if(this.document.updated_at != null) {
      query_document = "id=eq." + this.document.id + "&" + "updated_at=eq." + this.document.updated_at;
    } else {
      query_document = "id=eq." + this.document.id
    }
    // end

    // Check if account is has been already assignated to
    let resultCheck = this.checkIfAlreadySelected(accountSelected.id);
    if(resultCheck != true) {
      this.document.share_with.push(accountSelected.id);

      // Create obj to send
      let upload_document = {
        "updated_by": this.account_id,
        "updated_at": this.timezone.getCurrentDate(),
        "share_with": "{" + this.document.share_with.join(",") + "}"
      }
      // end
      //
      let subscription = this.service.update("documents", query_document, upload_document).subscribe(
        data => {
          this.messageService.setMessage('success');
          // Get current Date in words
          this.updated_at = this.timezone.getDistanceInWordsToNow(new Date());
          // end
          this.document.updated_at = upload_document.updated_at;
          this.document.accounts.push(accountSelected);
        },
        err => {
          this.messageService.setMessage('overwrite');
          console.log(err)
        }
      );

      this.sub.add(subscription);
    } else {
      this.messageService.setMessage('prohibition');
    }
    // end
  }

  update(form) {
    let form_copy: any = new Object(form.value);
    let query = "id=eq." + this.document.id;
    if(this.document.updated_at != null) {
      query += "&" + "updated_at=eq." + this.document.updated_at;
    }

    form_copy.updated_by = this.account_id;
    form_copy.updated_at = this.timezone.getCurrentDate();

    let subscription = this.service.update("documents", query, form_copy).subscribe(
      data => {
        this.messageService.setMessage('success');

        this.updated_at = this.timezone.getDistanceInWordsToNow(new Date());
        this.document.updated_at = form.value.updated_at;
      },
      err => {
        console.log(err);
        this.messageService.setMessage('rejected');
      }
    );

    this.sub.add(subscription);
  }

  getAssociatedAccounts() {
    let request_query = "id=in." + "(" + this.document.share_with.toString() + ")" + "&deleted=is.false" + "&status=eq.1" + "&select=id,firstname,lastname";
    let subscription = this.service.getRecords("accounts", request_query).subscribe(
      data => {
        this.document.accounts = data
      },
      err => {
        console.log(err)
      }
    );

    this.sub.add(subscription);
  }

  doYouHavePermission(share_with_id, logged_accound_id) {
    if(share_with_id.includes(logged_accound_id)) {
      return true
    } else {
      this.messageService.setMessage('without_permissions');
      this.navigation.redirect('/');
    }
  }

  getDocument(document_id) {
    let query = "id=eq." + document_id + "&select=*, created_by: accounts(id, firstname, lastname)";
    let subscription = this.service.getRecord("documents", query).subscribe(
      (data: any) => {
        if(this.doYouHavePermission(data.share_with, this.account_id)) {
          this.document = data;
          this.getAssociatedAccounts();
          this.created_at = this.timezone.getDistanceInWordsToNow(this.document.created_at);
          if(this.document.updated_at != null) {
            this.updated_at = this.timezone.getDistanceInWordsToNow(this.document.updated_at);
          }
        }
      },
      err => {
        console.log(err)
      }
    );

    this.sub.add(subscription);
  }

  getAccounts() {
    let query = "deleted=is.false" + "&status=eq." + 1 + "&id=neq." + this.account_id;
    let subscription = this.service.getRecords("accounts", query).subscribe(
      data => {
        this.accounts = data;
      }
    );

    this.sub.add(subscription);
  }

  ngOnInit() {
    this.getAccounts();
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



