import { Component, OnDestroy, Input, Output, EventEmitter } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Services
import { MessageService } from '../../../../services/message.service';
import { Service } from '../../../../services/http.service';

// Helpers
import { LocalStorageService } from '../../../../helpers/local-storage';

// Interface
import { Account } from '../../interface/account';
import { Ticket } from '../../../tickets/interface/ticket';

@Component({
  selector: '[accounts-list-row]',
  templateUrl: './row.component.html',
  styleUrls: ['./row.component.scss']
})
export class AccountListRowComponent implements OnDestroy {

  logged_account_id: number;

  @Input() account: Account;
  @Input() accounts: Account[] = [];
  @Input() tickets: Ticket[] = [];

  @Output() data = new EventEmitter();

  sub = new Subscription();

  constructor(
    private lsService: LocalStorageService,
    private service: Service,
    private messageService: MessageService
  ) {
    this.logged_account_id = +this.lsService.get("accountId");
  }


  checkIfLinkedTo() {
    let _this = this;
    let exist: boolean[] = [];
    this.tickets.forEach(function(ticket) {
      if(ticket.assignees_id.includes(_this.account.id) || ticket.created_by == _this.account.id) {
        exist.push(true);
        _this.messageService.setMessage('account_in_use');
      } else {
        exist.push(false);
      }
    });
    return exist
  }


  private deleteIt() {
    let query = "id=eq." + this.account.id;
    let index = this.accounts.indexOf(this.account);
    let delete_set = {
      'deleted': true
    };
    if(!this.checkIfLinkedTo().includes(true)) {
      let subscription = this.service.update("accounts", query, delete_set).subscribe(
        data => {
          this.accounts.splice(index, 1);
          this.messageService.setMessage('delete');

          this.data.emit(this.accounts);
        },
        err => {
          console.log(err)
        }
      );

      this.sub.add(subscription);
    }
  }

  // checkIfLinkedTo = () => {
  //   //let _this = this;
  //   let exist: boolean[] = [];
  //   this.tickets.forEach(function(ticket) {
  //     if(ticket.assignees_id.includes(this.account.id) || ticket.created_by == this.account.id) {
  //       exist.push(true);
  //       this.messageService.setMessage('account_in_use');
  //     } else {
  //       exist.push(false);
  //     }
  //   });
  //   return exist
  // }

  private statuSet() {
    let query = "id=eq." + this.account.id;
    let active_or_disactive = {
      'status': this.account.status
    };
    if(this.account.status == 1) {
      active_or_disactive.status = 0;
    } else {
      active_or_disactive.status = 1;
    }
    if(!this.checkIfLinkedTo().includes(true)) {
      let subscription = this.service.update("accounts", query, active_or_disactive).subscribe(
        data => {
          this.messageService.setMessage('success');
          this.account.status = active_or_disactive.status;
        },
        err => {
          console.log(err)
        }
      );

      this.sub.add(subscription);
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



