import { Component, OnInit, OnDestroy } from '@angular/core';
import { md5 } from '../../../helpers/md5';
import { addHours, getTime } from 'date-fns';
import * as itLocale from 'date-fns/locale/it/index.js';
import { Subscription } from "rxjs/Subscription";

// Helpers
import { LocalStorageService } from '../../../helpers/local-storage';
import { NavigationService } from '../../../helpers/navigation';

// Interfaces
import { Account } from '../interface/account';

// Services
import { Service } from '../../../services/http.service';
import { AccountService } from '../service/account.service';
import { MessageService } from '../../../services/message.service';

@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  styleUrls: ['./signin.component.scss']
})
export class SigninComponent implements OnInit, OnDestroy {

  sub = new Subscription();
  loading:boolean = false;

  constructor(
    private accountService: AccountService,
    private service: Service,
    private lsService: LocalStorageService,
    private messageService: MessageService,
    private navigation: NavigationService
  ) {}

  login(form) {
    let addingHours = addHours(new Date(), 8);
    let expire_date = getTime(addingHours);
    this.loading = true;
    form.value.psw = md5(form.value.psw);
    let query = "nickname=eq." + form.value.nickname + "&psw=eq." + form.value.psw + "&status=eq." + 1 + "&select=id, firstname, lastname, nickname, email, profile, status, avatar_color, online, settings: profile_settings(*)";
    let subscription = this.service.getRecord("accounts", query).subscribe(
      (data) => {
        this.accountService.setLoggedAccount(data);
        this.accountService.setOnlineStatus(data, true);
        this.accountService.setLocalStorage(data, expire_date);
      },
      (err) => {
        console.log(err);
        this.messageService.setMessage('access-denied');
        this.loading = false;
      }
    );

    this.sub.add(subscription);
  }

  ngOnInit() {
    if(getTime(new Date()) < +this.lsService.get('expires')) {
      this.navigation.redirect('/');
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



