import { Component, OnInit, Input } from '@angular/core';
import { Router} from "@angular/router";

// Helpers
import { LocalStorageService } from '../../../../helpers/local-storage';

@Component({
  selector: 'header-main-nav',
  templateUrl: './main-nav.component.html',
  styleUrls: ['./main-nav.component.scss']
})
export class HeaderMainNavComponent implements OnInit {

  @Input() account: Object;
  account_profile: number;

  constructor(
    public router: Router,
    private lsService: LocalStorageService
  ) { }

  ngOnInit() {
    this.account_profile = +this.lsService.get('accountProfile');
  }

}



