import { Component, OnDestroy } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../services/http.service';
import { MessageService } from '../../../services/message.service';
import { NotificationService } from '../../widgets/dropdown-icon-nav/service/notification.service';

// Helpers
import { LocalStorageService } from '../../../helpers/local-storage';
import { NavigationService } from '../../../helpers/navigation';
import { Timezone } from '../../../helpers/timezone';
import { StringHelperService } from '../../../helpers/string-helper';

@Component({
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class NotesAddComponent implements OnDestroy {

  loading: boolean = false;
  alarm: boolean = false;
  sub = new Subscription();

  constructor(
    private service: Service,
    private messageService: MessageService,
    private lsService: LocalStorageService,
    private navigation: NavigationService,
    private notificationService: NotificationService,
    private timezone: Timezone,
    private stringHelper: StringHelperService
  ) {}

  activeAlarm(value:boolean) {
    this.alarm = value;
  }

  clears(form) {
    form.reset();
  }

  getLocationHeaders(data) {
    let patt = /(\d+)/g;
    let id = data.headers.get("location");
    let id_match = id.match(patt);
    return +id_match[0];
  }

  create(form) {
    this.loading = true;
    form.value.account_id = +this.lsService.get("accountId");
    form.value.read = false;
    form.value.alarm_date = this.stringHelper.nullify(form.value.alarm_date);
    form.value.alarm_time = this.stringHelper.nullify(form.value.alarm_time);
    let subscription = this.service.create("notes", form.value).subscribe(
      data => {
        if(this.timezone.checkIfSameDay(new Date(), form.value.alarm_date)) {
          form.value.id = this.getLocationHeaders(data);
          this.notificationService.addElementToObservableArray(form.value);
        }
        this.loading = false;
        this.messageService.setMessage('success');
        this.navigation.redirect("/notes");
      },
      err => {
        console.log(err);
        this.loading = false;
        this.messageService.setMessage('rejected');
      }
    );

    this.sub.add(subscription);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



