import { Component } from '@angular/core';
import { OrderPipe } from 'ngx-order-pipe';

// Services
import { Service } from '../../../services/http.service';

// Interface
import { Addressbook } from '../interface/addressbook';

@Component({
  selector: 'addressbook-list',
  moduleId: module.id,
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class AddressbookListComponent {

  // config sort
  order: string = 'firstname';
  reverse: boolean = false;
  sortedCollection: any[];
  // end

  addressesbook: Addressbook[] = [];

  dataForRequestSearchComp = {
    "table": "addressbook",
    "parameters": ["firstname", "lastname", "company", "email", "mobilephone"],
    "condition": "archived=is.false"
  };

  config: any;

  constructor(
    private orderPipe: OrderPipe,
    private service: Service
  ) {
    this.loadAllItems("addressbook", "addressesbook", "not_archived");
    this.sortedCollection = orderPipe.transform(this.addressesbook, 'firstname');
    this.config = {
      id: 'addressbook-list',
			currentPage: 1,
			itemsPerPage: 10
    };
  }

  overWriteAddressesBook(data) {
    this.addressesbook = data;
    this.config.currentPage = 1;
    if(data.length >= 10) {
      this.config.itemsPerPage = 10
    } else {
      this.config.itemsPerPage = data.length;
    }
  }

  setOrder(value: string) {
    if(this.order === value) {
      this.reverse = !this.reverse;
    }
    this.order = value;
  }

  loadAllItems(table, variable, condition) {
    this.service.getAll(table, condition).subscribe(
      data => {
        this[variable] = data;
      }
    );
  }

}



