import { Component, OnDestroy, Input } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../../services/http.service';
import { MessageService } from '../../../../services/message.service';

// Interface
import { Document } from '../../interface/document';

@Component({
  selector: '[documents-list-row]',
  templateUrl: './row.component.html',
  styleUrls: ['./row.component.scss']
})
export class DocumentListRowComponent implements OnDestroy {

  @Input() document: Document;
  @Input() documents: Document[];
  @Input() account_id: number;
  @Input() account_profile: number;

  sub = new Subscription();

  constructor(
    private service: Service,
    private messageService: MessageService
  ) { }

  archiveIt() {
    const index = this.documents.indexOf(this.document);
    const archived = {
      "archived": true,
      "updated_by": this.account_id
    };
    let subscription = this.service.archived("documents", this.document.id, archived).subscribe(
      data => {
        this.documents.splice(index, 1);
        this.messageService.setMessage('archive');
      },
      err => {
        console.log(err)
      }
    );

    this.sub.add(subscription);
  }

  deleteIt() {
    const index = this.documents.indexOf(this.document);
    let subscription = this.service.delete("documents", "id", this.document.id).subscribe(
      data => {
        this.documents.splice(index, 1);
        this.messageService.setMessage('delete');
      },
      err => {
        console.log(err)
      }
    );

    this.sub.add(subscription);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



