import {Injectable} from "@angular/core";
import { Router} from "@angular/router";

@Injectable()
export class NavigationService {

  prev: number[] = [];
  next: number[] = [];

  constructor(
    public router: Router
  ) {}

  redirect(redirect_to) {
    this.router.navigate([redirect_to]);
  }

}



