import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
declare var $:any;

// Services
import { EventSourceListenerService } from '../../../services/event-source-listener.service';

@Component({
  selector: 'notify-alert',
  templateUrl: './notify-alert.component.html',
  styleUrls: ['./notify-alert.component.scss']
})
export class NotifyAlertComponent implements OnInit, OnDestroy {

  @Input() account;

  messages: any[] = [];
  subscription: Subscription;

  constructor(
    private eventSourceListenerService: EventSourceListenerService
  ) {}

  ngOnInit() {
    this.subscription = this.eventSourceListenerService.item$.subscribe(
      data => {
        if(this.account.settings[0].tickets_notifications === true) {
          this.messages = data;
        }
      }
    );
  }

  hideSidebar() {
    $(".notify-alert").toggleClass("hide");
  }

  deleteAllNotifications() {
    this.eventSourceListenerService.clearData();
  }

  ngOnDestroy() {
    // unsubscribe to ensure no memory leaks
    this.subscription.unsubscribe();
  }

}



