import { Component, Input } from '@angular/core';
declare var $:any;

// Services
import { NotificationService } from './service/notification.service';

@Component({
  selector: 'dropdown-icon-nav',
  templateUrl: './dropdown-icon-nav.component.html',
  styleUrls: ['./dropdown-icon-nav.component.scss']
})
export class DropdownIconNavComponent {

  @Input() icon_class: string;
  list: any[] = [];
  @Input() table: string;
  @Input() url: string;
  read: boolean;

  constructor(
    private notificationService: NotificationService
  ) {
    this.notificationService.item$.subscribe(
      data => {
        this.list = data;
        this.read = this.areThereNotVisitedItems();
      },
      err => {
        console.log(err)
      }
    )
  }

  areThereNotVisitedItems() {
    let sublist = this.list.map(function(item) {
      return item.read
    });
    if(sublist.includes(false)) {
      return true
    } else {
      return false
    }
  }

}



