import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuard } from './components/accounts/_guards/auth.guard';

import { CanDeactivateGuard } from './helpers/can-deactivate/can-deactivate.guard';

// Layouts
import { LoggedLayoutComponent } from './components/layout/logged-layout/logged-layout.component';
import { NotloggedLayoutComponent } from './components/layout/notlogged-layout/notlogged-layout.component';
import { DashboardComponent } from './components/layout/dashboard/dashboard.component';
// end

import { SigninComponent } from './components/accounts/signin/signin.component';
import { AddAccountComponent } from './components/accounts/add/add.component';
import { AccountsListComponent } from './components/accounts/list/list.component';
import { AccountDetailsComponent } from './components/accounts/details/details.component';
import { AccountProfileComponent } from './components/accounts/profile/profile.component';

import { AddressbookhomeComponent } from './components/address-book/addressbookhome/addressbookhome.component';
import { AddingAddressBookComponent } from './components/address-book/add/add.component';
// import { AddressbookListComponent } from './components/address-book/list/list.component';
// import { AddressbookArchivedComponent } from './components/address-book/archived/archived.component';
import { AddressbookDetailsComponent } from './components/address-book/details/details.component';

import { NoteshomeComponent } from './components/notes/noteshome/noteshome.component';
import { NotesDetailsComponent } from './components/notes/details/details.component';
import { NotesAddComponent } from './components/notes/add/add.component';
// import { NotesListComponent } from './components/notes/list/list.component';
// import { NotesArchivedComponent } from './components/notes/archived/archived.component';

import { TicketshomeComponent } from './components/tickets/ticketshome/ticketshome.component';
import { TicketAddComponent } from './components/tickets/add/add.component';
// import { TicketListComponent } from './components/tickets/list/list.component';
// import { TicketArchivedComponent } from './components/tickets/archived/archived.component';
import { TicketDetailsComponent } from './components/tickets/details/details.component';

import { DocumentshomeComponent } from './components/documents/documentshome/documentshome.component';
// import { DocumentListComponent } from './components/documents/list/list.component';
import { DocumentAddComponent } from './components/documents/add/add.component';
import { DocumentDetailsComponent } from './components/documents/details/details.component';
// import { DocumentsArchivedComponent } from './components/documents/archived/archived.component';

const appRoutes: Routes = [
  {
    path: '',
    component: LoggedLayoutComponent,
    canActivate:[AuthGuard],
    children: [
      {
        path: 'documents/add',
        component: DocumentAddComponent,
        data: {
          title: 'Aggiungi Documento',
          breadcrumb: 'Aggiungi Documento'
        }
      },
      {
        path: 'documents',
        component: DocumentshomeComponent,
        data: {
          title: 'Lista Documenti',
          breadcrumb: 'Lista Documenti'
        }
      },
      // {
      //   path: 'documents/archived',
      //   component: DocumentsArchivedComponent,
      //   data: {
      //     title: 'Lista Documenti archiviati',
      //     breadcrumb: 'Lista Documenti archiviati'
      //   }
      // },
      {
        path: 'documents/:id',
        component: DocumentDetailsComponent,
        data: {
          title: 'Dettaglio documento',
          breadcrumb: false
        },
        canDeactivate: [CanDeactivateGuard]
      },
      {
        path: '',
        component: DashboardComponent,
        pathMatch: 'full',
        data: {
          title: 'Dashboard',
          //breadcrumb: 'Home'
        }
      },
      {
        path: 'profile',
        component: AccountProfileComponent,
        data: {
          title: 'Profilo Utente',
          breadcrumb: 'Profilo'
        }
      },
      {
        path: 'accounts/add',
        component: AddAccountComponent,
        data: {
          title: 'Registrazione',
          breadcrumb: 'Aggiungi account'
        }
      },
      {
        path: 'accounts',
        component: AccountsListComponent,
        data: {
          title: 'Lista dipendenti',
          breadcrumb: 'Lista dipendenti'
        }
      },
      {
        path: 'accounts/:id',
        component: AccountDetailsComponent,
        data: {
          title: 'Dettaglio account',
          breadcrumb: "Dettaglio"
        },
        canDeactivate: [CanDeactivateGuard]
      },
      {
        path: 'addressbook/add',
        component: AddingAddressBookComponent,
        data: {
          title: 'Aggiungi in rubrica',
          breadcrumb: 'Aggiungi in rubrica'
        }
      },
      {
        path: 'addressbook',
        component: AddressbookhomeComponent,
        data: {
          title: 'Rubrica',
          breadcrumb: 'Rubrica'
        }
      },
      // {
      //   path: 'addressbook/archived',
      //   component: AddressbookArchivedComponent,
      //   data: {
      //     title: 'Lista indirizzi rubrica archiviati',
      //     breadcrumb: 'Archivio Rubrica'
      //   }
      // },
      {
        path: 'addressbook/:id',
        component: AddressbookDetailsComponent,
        data: {
          title: 'Dettaglio indirizzo rubrica',
          breadcrumb: 'Dettaglio'
        },
        canDeactivate: [CanDeactivateGuard]
      },
      {
        path: 'notes/add',
        component: NotesAddComponent,
        data: {
          title: 'Aggiungi appunti',
          breadcrumb: 'Aggiungi appunti'
        }
      },
      {
        path: 'notes',
        component: NoteshomeComponent,
        data: {
          title: 'Lista appunti',
          breadcrumb: 'Lista appunti'
        }
      },
      // {
      //   path: 'notes/archived',
      //   component: NotesArchivedComponent,
      //   data: {
      //     title: 'Lista appunti archiviati',
      //     breadcrumb: 'Lista appunti archiviati'
      //   }
      // },
      {
        path: 'notes/:id',
        component: NotesDetailsComponent,
        data: {
          title: 'Dettaglio appunto',
          breadcrumb: 'Dettaglio'
        },
        canDeactivate: [CanDeactivateGuard]
      },
      {
        path: 'tickets/add',
        component: TicketAddComponent,
        data: {
          title: 'Aggiungi ticket',
          breadcrumb: 'Aggiungi ticket'
        }
      },
      {
        path: 'tickets',
        component: TicketshomeComponent,
        data: {
          title: 'Lista ticket',
          breadcrumb: 'Lista ticket'
        }
      },
      {
        path: 'tickets/:id',
        component: TicketDetailsComponent,
        data: {
          title: 'Dettaglio ticket',
          breadcrumb: 'Dettaglio'
        }
      },
    ]
  },
  // No layout routes
  {
    path: 'signin',
    component: NotloggedLayoutComponent,
    children: [
      {
        path: '',
        component: SigninComponent,
        pathMatch: 'full',
        data: { title: 'Login' }
      }
    ]
  },

  // otherwise redirect to home
  {
    path: '**', redirectTo: ''
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(appRoutes)
  ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}



