import { Component, OnInit, OnChanges, OnDestroy, Input, SimpleChanges, SimpleChange } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Services
import { MessageService } from '../../../../services/message.service';
import { Service } from '../../../../services/http.service';
import { TicketsService } from '../../service/tickets.service';

// Helpers
import { Timezone } from '../../../../helpers/timezone';
import { NavigationService } from '../../../../helpers/navigation';

// Interface
import { Ticket } from '../../interface/ticket';

@Component({
  selector: '[tickets-list-row]',
  templateUrl: './row.component.html',
  styleUrls: ['./row.component.scss']
})
export class TicketListRowComponent implements OnInit, OnDestroy {

  @Input() ticket: Ticket;
  @Input() tickets: Ticket[] = [];
  @Input() account_profile: number;
  @Input() account_id: number;

  private _ticket: Ticket;

  read_it: boolean = true;

  sub = new Subscription();

  constructor(
    private service: Service,
    private messageService: MessageService,
    private ticketsService: TicketsService,
    private timezone: Timezone
  ) {}

  archiveIt() {
    let index = this.tickets.indexOf(this.ticket);
    let archived = {
      "archived": true,
      "updated_by": this.account_id,
      "updated_at": this.timezone.getCurrentDate()
    };
    let subscription = this.service.archived("tickets", this.ticket.id, archived).subscribe(
      data => {
        this.ticketsService.set(this.ticket, "archive");
        this.tickets.splice(index, 1);
        this.messageService.setMessage('archive');
      },
      err => {
        this.messageService.setMessage('rejected');
        console.log(err);
      }
    );

    this.sub.add(subscription);
  }

  haveYouAlreadyReadIt() {
    let res = this.ticket.read_by.includes(this.account_id);
    this.read_it = res;
  }

  // ngOnChanges(changes: SimpleChanges) {
  //   const ticket: SimpleChange = changes.ticket;
  //   console.log('vecchio ticket: ', ticket.previousValue);
  //   console.log('nuovo ticket: ', ticket.currentValue);
  //   this._ticket = ticket.currentValue;
  // }

  ngOnInit() {
    if(this.ticket.status != 5) {
      this.haveYouAlreadyReadIt()
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



