import { Component, OnDestroy } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../../services/http.service';

// Interface
import { Addressbook } from '../../../address-book/interface/addressbook';

@Component({
  selector: 'card-addressbook-list',
  templateUrl: './addressbook-list.component.html',
  styleUrls: ['./addressbook-list.component.scss']
})
export class CardAddressbookListComponent implements OnDestroy {

  contacts: Addressbook[] = [];
  dataForRequestSearchComp: Object;
  sub = new Subscription();

  constructor(
    private service: Service
  ) {
    this.loadAllItems("addressbook", "contacts", "not_archived");
    this.dataForRequestSearchComp = {
      "table": "addressbook",
      "parameters": ["firstname", "lastname", "company", "email", "mobilephone"],
      "condition": "archived=is.false"
    };
  }

  loadAllItems(table, variable, condition) {
    let subscription = this.service.getAll(table, condition).subscribe(
      data => {
        this[variable] = data;
      }
    );

    this.sub.add(subscription);
  }

  overWriteAddressesBook(data) {
    this.contacts = data;
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



