import { Component, OnDestroy, Input } from '@angular/core';
import { Observable } from 'rxjs';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../../services/http.service';
import { AccountService } from '../../service/account.service';
import { MessageService } from '../../../../services/message.service';

// Interfaces
import { Account } from '../../interface/account';

// Helpers
import { LocalStorageService } from '../../../../helpers/local-storage';

@Component({
  selector: 'account-personal-info',
  templateUrl: './personal-info.component.html',
  styleUrls: ['./personal-info.component.scss']
})
export class AccountPersonalInfoComponent implements OnDestroy {

  @Input() account: Account;
  sub = new Subscription();

  constructor(
    private service: Service,
    private accountService: AccountService,
    private messageService: MessageService,
    private lsService: LocalStorageService
  ) { }

  update() {
    let form_copy: any = Object.assign({}, this.account);
    delete form_copy.settings;
    let query = "id=eq." + this.account.id;
    let subscription = this.service.update("accounts", query, form_copy).subscribe(
      data => {
        if(form_copy.profile != this.account.profile) {
          this.accountService.logout(this.account);
        } else {
          this.accountService.setLoggedAccount(this.account);
          this.messageService.setMessage("success");
        }
      },
      err => {
        console.log(err);
        this.messageService.setMessage("rejected");
      }
    );

    this.sub.add(subscription);
  }

  revoke(){
    this.account = JSON.parse(this.lsService.get("account"));
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



