import { Injectable } from '@angular/core';

@Injectable()
export class StringHelperService {

  constructor() { }

  nullify(value) {
    if(value == "") {
  		return null
  	} else {
      return value
    }
  }

  trasformToNumber(data) {
    let result: any[] = data.map(function(item) {
      return +item
    });
    return result
  }

}



