import { Component, OnDestroy, Input } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../../services/http.service';
import { MessageService } from '../../../../services/message.service';
import { NotificationService } from '../../../widgets/dropdown-icon-nav/service/notification.service';

// Interface
import { Note } from '../../interface/note';

@Component({
  selector: '[notes-list-row]',
  templateUrl: './row.component.html',
  styleUrls: ['./row.component.scss']
})
export class NotesListRowComponent implements OnDestroy {

  @Input() note: Note;
  @Input() notes: Note[];
  sub = new Subscription();

  constructor(
    private service: Service,
    private messageService: MessageService,
    private notificationService: NotificationService
  ) { }

  archiveIt() {
    const index = this.notes.indexOf(this.note);
    const archived = {
      "archived": true
    };
    let subscription = this.service.archived("notes", this.note.id, archived).subscribe(
      data => {
        this.notes.splice(index, 1);
        this.messageService.setMessage('archive');
      },
      err => {
        console.log(err)
      }
    );

    this.sub.add(subscription);
  }

  deleteIt() {
    const index = this.notes.indexOf(this.note);
    let subscription = this.service.delete("notes", "id", this.note.id).subscribe(
      data => {
        let is_there = this.notificationService.isItIntoObservableArray(this.note);
        if(is_there) {
          this.notificationService.removeElementFromObservableArray(this.note);
        }
        this.notes.splice(index, 1);
        this.messageService.setMessage('delete');
      },
      err => {
        console.log(err)
      }
    );

    this.sub.add(subscription);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



