import { Component, OnDestroy } from '@angular/core';
import { Subscription } from "rxjs/Subscription";
declare var $:any;

// Services
import { Service } from '../../../services/http.service';
import { EventSourceListenerService } from '../../../services/event-source-listener.service';
import { NoteService } from '../../notes/service/note.service';
import { AccountService } from '../../accounts/service/account.service';

// Helpers
import { Timezone } from '../../../helpers/timezone';
import { LocalStorageService } from '../../../helpers/local-storage';

// Interfaces
import { Note } from '../../notes/interface/note';
import { Account } from '../../accounts/interface/account';

@Component({
  selector: 'app-logged-layout',
  templateUrl: './logged-layout.component.html',
  styleUrls: ['./logged-layout.component.scss']
})
export class LoggedLayoutComponent implements OnDestroy {

  logged_account: Account;
  //notes: Note[];

  sub = new Subscription();

  constructor(
    private noteService: NoteService,
    private lsService: LocalStorageService,
    private service: Service,
    private eventSourceListenerService: EventSourceListenerService,
    private timezoneHelper: Timezone,
    private accountService: AccountService
  ) {
    this.eventSourceListenerService.connect();
    let subscription = this.accountService.logged_account.subscribe(
      (res: any) => {
        if(res == null) {
          this.logged_account = JSON.parse(this.lsService.get("account"));
        } else {
          this.logged_account = res;
        }
      }
    );

    this.sub.add(subscription);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



