import { Component, OnDestroy, Input, Output, EventEmitter } from '@angular/core';
import { distanceInWordsToNow, parse, addHours, getTime } from 'date-fns';
import * as itLocale from 'date-fns/locale/it/index.js';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../services/http.service';
import { MessageService } from '../../../services/message.service';

// Interfaces
import { Ticket } from '../interface/ticket';

// Helpers
import { Timezone } from '../../../helpers/timezone'

@Component({
  selector: 'assignees-list',
  templateUrl: './assignees-list.component.html',
  styleUrls: ['./assignees-list.component.scss']
})
export class AssigneesListComponent implements OnDestroy {

  @Input() ticket;
  @Input() account_id: number;
  //@Output() updated_ticket = new EventEmitter();

  sub = new Subscription();

  constructor(
    private service: Service,
    public timezone: Timezone,
    private messageService: MessageService
  ) { }

  removeAssignee(assignee) {
    // Request
    let query_ticket;
    if(this.ticket.updated_at != null) {
      query_ticket = "id=eq." + this.ticket.id + "&" + "updated_at=eq." + this.ticket.updated_at;
    } else {
      query_ticket = "id=eq." + this.ticket.id
    }
    // end

    // Remove assigne id from model for assignees list
    this.ticket.assignees_id.splice(this.ticket.assignees_id.indexOf(assignee.id), 1);
    // end

    // Create obj to send for upload
    let upload_ticket = {
      "updated_by": this.account_id,
      "updated_at": this.timezone.getCurrentDate(),
      "assignees_id": "{" + this.ticket.assignees_id.join(",") + "}"
    }
    // end

    let subscription = this.service.update("tickets", query_ticket, upload_ticket).subscribe(
      data => {
        this.messageService.setMessage('success');
        this.ticket.updated_at = upload_ticket.updated_at;
        // Remove assignee selected from view
        let index = this.ticket.assignees.indexOf(assignee);
        this.ticket.assignees.splice(index, 1);
        // end
        // this.updated_ticket.emit({
        //   "ticket": this.ticket,
        //   "updated_at": this.timezone.getDistanceInWordsToNow(new Date())
        // })
      },
      err => {
        this.messageService.setMessage('overwrite');
        console.log(err);
      }
    );

    this.sub.add(subscription);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



