import { Component } from '@angular/core';

// Services
import { Service } from '../../../services/http.service';

// Interface
import { Addressbook } from '../interface/addressbook';

@Component({
  selector: 'addressbook-archived',
  templateUrl: './archived.component.html',
  styleUrls: ['./archived.component.scss']
})
export class AddressbookArchivedComponent {

  addressesbook: Addressbook[] = [];

  dataForRequestSearchComp = {
    "table": "addressbook",
    "parameters": ["firstname", "lastname", "company", "email", "mobilephone"],
    "condition": "archived=is.true"
  };

  config: any;

  constructor(
    private service: Service
  ) {
    this.loadAllItems("addressbook", "addressesbook", "archived");
    this.config = {
      id: 'addressbook-archived',
			currentPage: 1,
			itemsPerPage: 10
    };
  }

  overWriteListResult(data) {
    this.addressesbook = data;
    this.config.currentPage = 1;
    if(data.length < 10) {
      this.config.itemsPerPage = data.length;
    }
  }

  loadAllItems(table, variable, condition) {
    this.service.getAll(table, condition).subscribe(
      data => {
        this[variable] = data;
      }
    );
  }

}



