import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Subscription } from "rxjs/Subscription";
declare var $:any;

// Services
import { Service } from '../../../../services/http.service';

// Interface
import { Account } from '../../../accounts/interface/account';
import { Document } from '../../../documents/interface/document';

@Component({
  selector: 'card-document-list',
  templateUrl: './document-list.component.html',
  styleUrls: ['./document-list.component.scss']
})
export class CardDocumentListComponent implements OnInit, OnDestroy {

  @Input() logged_account: Account;
  documents: Document[] = [];

  sub = new Subscription();

  constructor(
    private service: Service
  ) {}

  getDocuments() {
    let query = "share_with=cs." +"{" + this.logged_account.id + "}" + "&" + "archived=" + "is.false" + "&limit=20" + "&select=*, account: accounts(firstname, lastname)";;
    let subscription = this.service.getRecords("documents", query).subscribe(
      data => {
        this.documents = data;
      },
      err => {
        console.log(err);
      }
    );

    this.sub.add(subscription);
  }

  ngOnInit() {
    this.getDocuments();
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



