import {Injectable} from "@angular/core";

@Injectable()
export class CheckObject {

  constructor() {}

  isBlank(obj) {
    return Object.keys(obj).length === 0 && obj.constructor === Object;
  }

  getNotEmptyPropertiesValue(obj) {
    let data = {};
    for (var key in obj) {
      if (obj[key] !== null && obj[key] != "" && obj[key] !== undefined) {
        data[key] = obj[key]
      }
    }
    return data;
  }

}



